// ============================================================================
//  Subscription Policies Dashboard -  
// ============================================================================

import React, { useState, useEffect, useCallback } from 'react';
import {
  Shield,
  Settings,
  Save,
  RefreshCw,
  Plus,
  History,
  Check,
  X,
  ChevronDown,
  ChevronUp,
  AlertTriangle,
  Zap,
  Users,
  FileText,
  Search,
  Brain,
  Target,
  Layers
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPost } from '../../utils/api';

// Types
interface TierPolicy {
  id: string;
  tier: string;
  monthly_tokens_limit: number;
  monthly_cases_limit: number | null;
  enabled_services: string[];
  service_caps: Record<string, Record<string, number>>;
  effective_from: string;
  is_active: boolean;
  notes: string | null;
  created_by_user_id: string | null;
  created_at: string;
}

interface ServiceInfo {
  name: string;
  display_name: string;
  description: string;
  category: string;
  caps: string[];
}

interface PreviewResult {
  allowed: boolean;
  service_enabled: boolean;
  tokens_check: {
    allowed: boolean;
    limit: number;
    used: number;
    remaining: number;
    estimated: number;
  };
  cap_check?: {
    allowed: boolean;
    limit: number | null;
    used: number;
    remaining: number;
    requested: number;
  };
  reason: string | null;
}

// Service category icons
const categoryIcons: Record<string, React.ReactNode> = {
  core: <Zap className="w-4 h-4" />,
  analysis: <Brain className="w-4 h-4" />,
  search: <Search className="w-4 h-4" />,
  processing: <Layers className="w-4 h-4" />,
  reports: <FileText className="w-4 h-4" />,
  editing: <FileText className="w-4 h-4" />,
  output: <FileText className="w-4 h-4" />,
  advanced: <Target className="w-4 h-4" />,
  admin: <Shield className="w-4 h-4" />
};

// Format numbers
const formatNumber = (n: number): string => {
  if (n >= 1000000) return `${(n / 1000000).toFixed(1)}M`;
  if (n >= 1000) return `${(n / 1000).toFixed(0)}K`;
  return n.toString();
};

// Main Component
const SubscriptionPoliciesDashboard: React.FC = () => {
  const [loading, setLoading] = useState(false);
  const [policies, setPolicies] = useState<TierPolicy[]>([]);
  const [services, setServices] = useState<ServiceInfo[]>([]);
  const [selectedTier, setSelectedTier] = useState<string>('pro');
  const [expandedTier, setExpandedTier] = useState<string | null>(null);
  const [showHistory, setShowHistory] = useState(false);
  const [policyHistory, setPolicyHistory] = useState<TierPolicy[]>([]);

  // Form state for creating/editing policy
  const [formState, setFormState] = useState({
    monthly_tokens_limit: 500000,
    monthly_cases_limit: 50 as number | null,
    enabled_services: [] as string[],
    service_caps: {} as Record<string, Record<string, number>>,
    notes: ''
  });

  // Preview state
  const [previewUserId, setPreviewUserId] = useState('');
  const [previewService, setPreviewService] = useState('');
  const [previewTokens, setPreviewTokens] = useState(1000);
  const [previewResult, setPreviewResult] = useState<PreviewResult | null>(null);

  // Fetch policies
  const fetchPolicies = useCallback(async () => {
    try {
      setLoading(true);
      const response = await apiGet('/admin/subscriptions/policies');
      setPolicies(response || []);
    } catch (error) {
      console.error('Failed to fetch policies:', error);
      toast.error('  ');
    } finally {
      setLoading(false);
    }
  }, []);

  // Fetch services
  const fetchServices = useCallback(async () => {
    try {
      const response = await apiGet('/admin/subscriptions/services');
      setServices(response || []);
    } catch (error) {
      console.error('Failed to fetch services:', error);
    }
  }, []);

  // Fetch policy history for a tier
  const fetchHistory = async (tier: string) => {
    try {
      const response = await apiGet(`/admin/subscriptions/policies/${tier}/history`);
      setPolicyHistory(response || []);
      setShowHistory(true);
    } catch (error) {
      console.error('Failed to fetch history:', error);
      toast.error('  ');
    }
  };

  // Load policy into form
  const loadPolicyToForm = (policy: TierPolicy) => {
    setFormState({
      monthly_tokens_limit: policy.monthly_tokens_limit,
      monthly_cases_limit: policy.monthly_cases_limit,
      enabled_services: [...policy.enabled_services],
      service_caps: JSON.parse(JSON.stringify(policy.service_caps)),
      notes: policy.notes || ''
    });
    setSelectedTier(policy.tier);
  };

  // Save policy
  const savePolicy = async () => {
    try {
      setLoading(true);
      await apiPost('/admin/subscriptions/policies', {
        tier: selectedTier,
        monthly_tokens_limit: formState.monthly_tokens_limit,
        monthly_cases_limit: formState.monthly_cases_limit,
        enabled_services: formState.enabled_services,
        service_caps: formState.service_caps,
        notes: formState.notes || null
      });
      toast.success(' .   .');
      fetchPolicies();
    } catch (error) {
      console.error('Failed to save policy:', error);
      toast.error('  ');
    } finally {
      setLoading(false);
    }
  };

  // Activate specific policy
  const activatePolicy = async (tier: string, policyId: string) => {
    try {
      await apiPost(`/admin/subscriptions/policies/${tier}/activate/${policyId}`, {});
      toast.success(' ');
      fetchPolicies();
      fetchHistory(tier);
    } catch (error) {
      console.error('Failed to activate policy:', error);
      toast.error('  ');
    }
  };

  // Preview entitlement check
  const runPreview = async () => {
    if (!previewUserId || !previewService) {
      toast.error('    ');
      return;
    }
    try {
      const response = await apiPost('/admin/subscriptions/preview', {
        user_id: previewUserId,
        service_name: previewService,
        estimated_tokens: previewTokens,
        cap_key: null,
        cap_delta: 1
      });
      setPreviewResult(response);
    } catch (error) {
      console.error('Preview failed:', error);
      toast.error('  ');
    }
  };

  // Toggle service in form
  const toggleService = (serviceName: string) => {
    setFormState(prev => ({
      ...prev,
      enabled_services: prev.enabled_services.includes(serviceName)
        ? prev.enabled_services.filter(s => s !== serviceName)
        : [...prev.enabled_services, serviceName]
    }));
  };

  // Update service cap in form
  const updateServiceCap = (serviceName: string, capKey: string, value: number | null) => {
    setFormState(prev => {
      const newCaps = { ...prev.service_caps };
      if (value === null) {
        if (newCaps[serviceName]) {
          delete newCaps[serviceName][capKey];
          if (Object.keys(newCaps[serviceName]).length === 0) {
            delete newCaps[serviceName];
          }
        }
      } else {
        if (!newCaps[serviceName]) {
          newCaps[serviceName] = {};
        }
        newCaps[serviceName][capKey] = value;
      }
      return { ...prev, service_caps: newCaps };
    });
  };

  // Initial load
  useEffect(() => {
    fetchPolicies();
    fetchServices();
  }, [fetchPolicies, fetchServices]);

  // Load selected tier policy into form
  useEffect(() => {
    const policy = policies.find(p => p.tier === selectedTier && p.is_active);
    if (policy) {
      loadPolicyToForm(policy);
    }
  }, [selectedTier, policies]);

  // Group policies by tier
  const policyByTier = policies.reduce((acc, p) => {
    if (p.is_active) {
      acc[p.tier] = p;
    }
    return acc;
  }, {} as Record<string, TierPolicy>);

  const tiers = ['free', 'basic', 'pro', 'professional', 'firm', 'enterprise', 'admin'];

  return (
    <div className="p-6 space-y-6 bg-gray-900 min-h-screen text-white" dir="rtl">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold flex items-center gap-2">
            <Shield className="w-6 h-6 text-blue-400" />
             
          </h1>
          <p className="text-gray-400 mt-1">
                    /.
          </p>
        </div>
        <button
          onClick={fetchPolicies}
          disabled={loading}
          className="flex items-center gap-2 px-4 py-2 bg-gray-700 hover:bg-gray-600 rounded-lg transition-colors"
        >
          <RefreshCw className={`w-4 h-4 ${loading ? 'animate-spin' : ''}`} />
          
        </button>
      </div>

      {/* Tier Selector */}
      <div className="flex gap-2 flex-wrap">
        {tiers.map(tier => (
          <button
            key={tier}
            onClick={() => setSelectedTier(tier)}
            className={`px-4 py-2 rounded-lg font-medium transition-colors ${
              selectedTier === tier
                ? 'bg-blue-600 text-white'
                : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
            }`}
          >
            {tier.charAt(0).toUpperCase() + tier.slice(1)}
          </button>
        ))}
      </div>

      {/* Main Content - Two Columns */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Policy Editor */}
        <div className="bg-gray-800 rounded-xl p-6 space-y-6">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-semibold">  - {selectedTier}</h2>
            <button
              onClick={() => fetchHistory(selectedTier)}
              className="text-sm text-blue-400 hover:text-blue-300 flex items-center gap-1"
            >
              <History className="w-4 h-4" />
              
            </button>
          </div>

          {/* Token Limit */}
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
                
            </label>
            <input
              type="number"
              value={formState.monthly_tokens_limit}
              onChange={e => setFormState(prev => ({ ...prev, monthly_tokens_limit: parseInt(e.target.value) || 0 }))}
              className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white"
            />
            <p className="text-xs text-gray-500 mt-1">
              {formatNumber(formState.monthly_tokens_limit)}  
            </p>
          </div>

          {/* Cases Limit */}
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
                 ( =  )
            </label>
            <input
              type="number"
              value={formState.monthly_cases_limit ?? ''}
              onChange={e => setFormState(prev => ({
                ...prev,
                monthly_cases_limit: e.target.value ? parseInt(e.target.value) : null
              }))}
              placeholder=" "
              className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white"
            />
          </div>

          {/* Services */}
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
               
            </label>
            <p className="text-xs text-gray-500 mb-3">
                    .
            </p>
            <div className="grid grid-cols-2 gap-2 max-h-60 overflow-y-auto">
              {services.map(service => (
                <button
                  key={service.name}
                  onClick={() => toggleService(service.name)}
                  className={`flex items-center gap-2 p-2 rounded-lg text-sm transition-colors ${
                    formState.enabled_services.includes(service.name)
                      ? 'bg-green-600/20 border border-green-500 text-green-300'
                      : 'bg-gray-700 border border-gray-600 text-gray-400 hover:bg-gray-600'
                  }`}
                >
                  {formState.enabled_services.includes(service.name) ? (
                    <Check className="w-4 h-4" />
                  ) : (
                    <X className="w-4 h-4" />
                  )}
                  {categoryIcons[service.category]}
                  <span className="truncate">{service.display_name}</span>
                </button>
              ))}
            </div>
          </div>

          {/* Service Caps */}
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
               -
            </label>
            <div className="space-y-2">
              {services.filter(s => s.caps.length > 0).map(service => (
                <div key={service.name} className="bg-gray-700 rounded-lg p-3">
                  <div className="font-medium text-sm mb-2">{service.display_name}</div>
                  <div className="grid grid-cols-2 gap-2">
                    {service.caps.map(cap => (
                      <div key={cap} className="flex items-center gap-2">
                        <span className="text-xs text-gray-400 truncate flex-1">{cap}:</span>
                        <input
                          type="number"
                          value={formState.service_caps[service.name]?.[cap] ?? ''}
                          onChange={e => updateServiceCap(
                            service.name,
                            cap,
                            e.target.value ? parseInt(e.target.value) : null
                          )}
                          placeholder=""
                          className="w-20 bg-gray-600 border border-gray-500 rounded px-2 py-1 text-sm text-white"
                        />
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Notes */}
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              
            </label>
            <textarea
              value={formState.notes}
              onChange={e => setFormState(prev => ({ ...prev, notes: e.target.value }))}
              className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white h-20"
              placeholder=" ..."
            />
          </div>

          {/* Save Button */}
          <button
            onClick={savePolicy}
            disabled={loading}
            className="w-full flex items-center justify-center gap-2 px-4 py-3 bg-blue-600 hover:bg-blue-500 disabled:bg-gray-600 rounded-lg font-medium transition-colors"
          >
            <Save className="w-5 h-5" />
             
          </button>
        </div>

        {/* Preview & Status */}
        <div className="space-y-6">
          {/* Current Policies Overview */}
          <div className="bg-gray-800 rounded-xl p-6">
            <h2 className="text-xl font-semibold mb-4">  </h2>
            <div className="space-y-3">
              {tiers.map(tier => {
                const policy = policyByTier[tier];
                return (
                  <div
                    key={tier}
                    className={`flex items-center justify-between p-3 rounded-lg cursor-pointer transition-colors ${
                      expandedTier === tier ? 'bg-gray-700' : 'bg-gray-750 hover:bg-gray-700'
                    }`}
                    onClick={() => setExpandedTier(expandedTier === tier ? null : tier)}
                  >
                    <div className="flex items-center gap-3">
                      <span className="font-medium">{tier}</span>
                      {policy ? (
                        <span className="text-xs text-green-400 flex items-center gap-1">
                          <Check className="w-3 h-3" />
                          
                        </span>
                      ) : (
                        <span className="text-xs text-yellow-400 flex items-center gap-1">
                          <AlertTriangle className="w-3 h-3" />
                           
                        </span>
                      )}
                    </div>
                    <div className="flex items-center gap-4 text-sm text-gray-400">
                      {policy && (
                        <>
                          <span>{formatNumber(policy.monthly_tokens_limit)} </span>
                          <span>{policy.enabled_services.length} </span>
                        </>
                      )}
                      {expandedTier === tier ? <ChevronUp className="w-4 h-4" /> : <ChevronDown className="w-4 h-4" />}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>

          {/* Preview Widget */}
          <div className="bg-gray-800 rounded-xl p-6">
            <h2 className="text-xl font-semibold mb-4"> </h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm text-gray-400 mb-1"> </label>
                <input
                  type="text"
                  value={previewUserId}
                  onChange={e => setPreviewUserId(e.target.value)}
                  placeholder="user_id..."
                  className="w-full bg-gray-700 border border-gray-600 rounded-lg px-3 py-2 text-white"
                />
              </div>
              <div>
                <label className="block text-sm text-gray-400 mb-1"></label>
                <select
                  value={previewService}
                  onChange={e => setPreviewService(e.target.value)}
                  className="w-full bg-gray-700 border border-gray-600 rounded-lg px-3 py-2 text-white"
                >
                  <option value=""> ...</option>
                  {services.map(s => (
                    <option key={s.name} value={s.name}>{s.display_name}</option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm text-gray-400 mb-1"> </label>
                <input
                  type="number"
                  value={previewTokens}
                  onChange={e => setPreviewTokens(parseInt(e.target.value) || 0)}
                  className="w-full bg-gray-700 border border-gray-600 rounded-lg px-3 py-2 text-white"
                />
              </div>
              <button
                onClick={runPreview}
                className="w-full px-4 py-2 bg-purple-600 hover:bg-purple-500 rounded-lg font-medium"
              >
                 
              </button>

              {previewResult && (
                <div className={`p-4 rounded-lg ${
                  previewResult.allowed ? 'bg-green-900/30 border border-green-700' : 'bg-red-900/30 border border-red-700'
                }`}>
                  <div className="flex items-center gap-2 font-medium mb-2">
                    {previewResult.allowed ? (
                      <><Check className="w-5 h-5 text-green-400" /> </>
                    ) : (
                      <><X className="w-5 h-5 text-red-400" /> </>
                    )}
                  </div>
                  {previewResult.reason && (
                    <p className="text-sm text-gray-300">{previewResult.reason}</p>
                  )}
                  <div className="mt-2 text-xs text-gray-400">
                    <div>: {previewResult.service_enabled ? '' : ''}</div>
                    <div>: {previewResult.tokens_check.used}/{previewResult.tokens_check.limit} ( {previewResult.tokens_check.remaining})</div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* History Modal */}
      {showHistory && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50" onClick={() => setShowHistory(false)}>
          <div className="bg-gray-800 rounded-xl p-6 max-w-2xl w-full max-h-[80vh] overflow-y-auto" onClick={e => e.stopPropagation()}>
            <h2 className="text-xl font-semibold mb-4">  - {selectedTier}</h2>
            <div className="space-y-3">
              {policyHistory.map((policy, i) => (
                <div
                  key={policy.id}
                  className={`p-4 rounded-lg ${policy.is_active ? 'bg-blue-900/30 border border-blue-700' : 'bg-gray-700'}`}
                >
                  <div className="flex items-center justify-between mb-2">
                    <div className="flex items-center gap-2">
                      {policy.is_active && <span className="text-xs bg-blue-600 px-2 py-0.5 rounded"></span>}
                      <span className="text-sm text-gray-400">
                        {new Date(policy.effective_from).toLocaleDateString('he-IL')}
                      </span>
                    </div>
                    {!policy.is_active && (
                      <button
                        onClick={() => activatePolicy(selectedTier, policy.id)}
                        className="text-sm text-blue-400 hover:text-blue-300"
                      >
                        
                      </button>
                    )}
                  </div>
                  <div className="text-sm">
                    <span className="text-gray-400">:</span> {formatNumber(policy.monthly_tokens_limit)} |
                    <span className="text-gray-400 ml-2">:</span> {policy.enabled_services.length}
                  </div>
                  {policy.notes && <p className="text-xs text-gray-500 mt-1">{policy.notes}</p>}
                </div>
              ))}
            </div>
            <button
              onClick={() => setShowHistory(false)}
              className="mt-4 w-full px-4 py-2 bg-gray-600 hover:bg-gray-500 rounded-lg"
            >
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default SubscriptionPoliciesDashboard;
