// UserSubscriptionModal.tsx - Modal for updating user subscription
import React, { useState } from 'react';
import { Crown, Save, X } from 'lucide-react';
import ModalBase from '../ModalBase';
import { toast } from 'sonner';
import { apiPut } from '../../utils/api';
import { logger } from '../../utils/logger';

interface UserSubscriptionModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
  userId: string;
  userEmail: string;
  currentTier: string;
}

const TIERS = [
  { value: 'free', label: 'Free', description: '  ' },
  { value: 'basic', label: 'Basic', description: '  ' },
  { value: 'professional', label: 'Professional', description: ' ' },
  { value: 'enterprise', label: 'Enterprise', description: ' ' },
  { value: 'admin', label: 'Admin', description: '  ' }
];

const UserSubscriptionModal: React.FC<UserSubscriptionModalProps> = ({
  isOpen,
  onClose,
  onSuccess,
  userId,
  userEmail,
  currentTier
}) => {
  const [selectedTier, setSelectedTier] = useState(currentTier);
  const [saving, setSaving] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (selectedTier === currentTier) {
      toast.info('   - Tier ');
      onClose();
      return;
    }

    setSaving(true);
    try {
      await apiPut(`/api/admin/users/${userId}/subscription`, { tier: selectedTier }, { showErrorToast: false });
      toast.success(`Tier   -${selectedTier}`);
      onSuccess();
      onClose();
    } catch (error) {
      logger.error('Failed to update subscription:', error);
      toast.error('  ');
    } finally {
      setSaving(false);
    }
  };

  return (
    <ModalBase isOpen={isOpen} onClose={onClose} title="  " size="md">
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* User Info */}
        <div className="bg-white/5 rounded-lg p-4">
          <div className="text-sm text-slate-400"></div>
          <div className="text-white font-medium">{userEmail}</div>
          <div className="text-sm text-slate-400 mt-1">
            Tier : <span className="text-white font-semibold">{currentTier}</span>
          </div>
        </div>

        {/* Tier Selection */}
        <div>
          <label className="block text-white font-medium mb-3"> Tier :</label>
          <div className="space-y-2">
            {TIERS.map(tier => (
              <label
                key={tier.value}
                className={`flex items-start gap-3 p-4 rounded-lg border-2 cursor-pointer transition-all ${
                  selectedTier === tier.value
                    ? 'border-purple-500 bg-purple-500/10'
                    : 'border-white/10 bg-white/5 hover:bg-white/10'
                }`}
              >
                <input
                  type="radio"
                  name="tier"
                  value={tier.value}
                  checked={selectedTier === tier.value}
                  onChange={(e) => setSelectedTier(e.target.value)}
                  className="mt-1"
                />
                <div className="flex-1">
                  <div className="flex items-center gap-2">
                    {tier.value === 'admin' && <Crown className="w-4 h-4 text-yellow-400" />}
                    <span className="text-white font-medium">{tier.label}</span>
                  </div>
                  <div className="text-sm text-slate-400 mt-1">{tier.description}</div>
                </div>
              </label>
            ))}
          </div>
        </div>

        {/* Actions */}
        <div className="flex gap-3 justify-end pt-4 border-t border-white/10">
          <button
            type="button"
            onClick={onClose}
            className="px-6 py-2 bg-slate-700 hover:bg-slate-600 text-white rounded-lg transition-colors flex items-center gap-2"
          >
            <X className="w-4 h-4" />
            
          </button>
          <button
            type="submit"
            disabled={saving || selectedTier === currentTier}
            className="px-6 py-2 bg-purple-600 hover:bg-purple-700 disabled:bg-slate-700 disabled:cursor-not-allowed text-white rounded-lg transition-colors flex items-center gap-2"
          >
            <Save className="w-4 h-4" />
            {saving ? '...' : ''}
          </button>
        </div>
      </form>
    </ModalBase>
  );
};

export default UserSubscriptionModal;
