/**
 * AgentsDashboard - Full Admin Dashboard for Agent Monitoring
 * Shows all agents, their activity, statistics, and performance
 */

import React, { useState, useEffect, useCallback } from 'react';
import {
  Bot,
  Brain,
  FileText,
  Search,
  Users,
  AlertTriangle,
  Scale,
  Shield,
  Target,
  Eye,
  CheckCircle,
  XCircle,
  Loader2,
  RefreshCw,
  Activity,
  Clock,
  Zap,
  TrendingUp,
  BarChart3
} from 'lucide-react';
import { apiGet } from '../../utils/api';
import { logger } from '../../utils/logger';

// Agent type to icon mapping - all 37 agents
const AGENT_ICONS: Record<string, any> = {
  // Core Agents (4)
  ocr: FileText,
  claims: Search,
  entities: Users,
  contradictions: AlertTriangle,
  // Secondary Agents (5)
  intelligence: Brain,
  profiler: Users,
  timeline: Clock,
  cross_exam: Target,
  strategy: Target,
  // Analysis Agents (6)
  risk_assessment: Shield,
  red_flags: AlertTriangle,
  evidence_strength: Scale,
  gap_analysis: Search,
  narrative: FileText,
  verification: CheckCircle,
  // Legal Agents (3)
  precedent: Scale,
  adversarial: Target,
  disputes: AlertTriangle,
  // Intelligence Agents (4)
  summary: FileText,
  insights: Brain,
  knowledge: Brain,
  legal_knowledge: Brain, // legacy alias
  // Case-Specific Agents (4)
  settlement: Scale,
  witness_credibility: Eye,
  court_behavior: Scale,
  // System Agents (5)
  admin: Shield,
  monitoring: Activity,
  memory: Brain,
  learning: TrendingUp,
  cache: Zap,
  // Utility Agents (7)
  chat_intent: Search,
  graph: BarChart3,
  deduplication: CheckCircle,
  enrichment: TrendingUp,
  legal_patterns: Search,
  export: FileText,
  document_analysis: FileText,
  // Default
  default: Bot
};

interface AgentActivity {
  agent_type: string;
  agent_name: string;
  status: 'running' | 'pending' | 'completed' | 'failed' | 'idle';
  case_id?: string;
  case_name?: string;
  user_email?: string;
  progress?: number;
  current_task?: string;
  started_at?: string;
  duration_ms?: number;
  tokens_used?: number;
  cost_dollars?: number;
}

interface SystemOverview {
  total_agents: number;
  active_agents: number;
  pending_tasks: number;
  cases_being_analyzed: number;
  total_tokens_today: number;
  total_cost_today?: number;
}

export default function AgentsDashboard() {
  const [activities, setActivities] = useState<AgentActivity[]>([]);
  const [overview, setOverview] = useState<SystemOverview | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [lastUpdate, setLastUpdate] = useState<Date | null>(null);
  const [autoRefresh, setAutoRefresh] = useState(true);

  // Fetch agent activity
  const fetchActivity = useCallback(async () => {
    try {
      setError(null);
      const data = await apiGet('/api/admin/agents/activity', { showErrorToast: false });

      setActivities(data.activities || []);
      setOverview(data.overview || null);
      setLastUpdate(new Date());
    } catch (err) {
      logger.error('Failed to fetch agent activity:', err);
      setError('   ');
    } finally {
      setLoading(false);
    }
  }, []);

  // Initial fetch and polling
  useEffect(() => {
    fetchActivity();

    // Poll every 15 seconds (reduced from 5s to prevent rate limiting)
    let interval: NodeJS.Timeout | null = null;
    if (autoRefresh) {
      interval = setInterval(fetchActivity, 15000);
    }

    return () => {
      if (interval) clearInterval(interval);
    };
  }, [fetchActivity, autoRefresh]);

  const getAgentIcon = (agentType: string) => {
    return AGENT_ICONS[agentType] || AGENT_ICONS.default;
  };

  const formatDuration = (ms: number) => {
    if (ms < 1000) return `${ms}ms`;
    if (ms < 60000) return `${(ms / 1000).toFixed(1)}s`;
    return `${(ms / 60000).toFixed(1)}m`;
  };

  const activeAgents = activities.filter(a => a.status === 'running');
  const pendingAgents = activities.filter(a => a.status === 'pending');
  const completedAgents = activities.filter(a => a.status === 'completed');
  const failedAgents = activities.filter(a => a.status === 'failed');
  const idleAgents = activities.filter(a => a.status === 'idle');

  if (loading && activities.length === 0) {
    return (
      <div className="p-8 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-12 h-12 text-purple-500 animate-spin mx-auto mb-4" />
          <p className="text-white text-lg">  ...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6" dir="rtl">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-6 flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-white mb-2"> </h1>
            <p className="text-slate-400">       </p>
          </div>
          <div className="flex items-center gap-3">
            <label className="flex items-center gap-2 text-sm text-white/70">
              <input
                type="checkbox"
                checked={autoRefresh}
                onChange={(e) => setAutoRefresh(e.target.checked)}
                className="rounded bg-white/10 border-white/20"
              />
               
            </label>
            <button
              onClick={fetchActivity}
              disabled={loading}
              className="px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-colors flex items-center gap-2"
            >
              <RefreshCw size={18} className={loading ? 'animate-spin' : ''} />
              
            </button>
          </div>
        </div>

        {/* Error State */}
        {error && (
          <div className="mb-6 bg-red-500/10 border border-red-500/30 rounded-lg p-4 text-center">
            <AlertTriangle className="w-8 h-8 text-red-500 mx-auto mb-2" />
            <p className="text-red-400">{error}</p>
          </div>
        )}

        {/* Overview Cards */}
        {overview && (
          <div className="grid grid-cols-1 md:grid-cols-5 gap-4 mb-6">
            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-3">
                <Bot className="h-8 w-8 text-purple-400" />
                <div>
                  <p className="text-white/70 text-sm"> </p>
                  <p className="text-3xl font-bold text-white">{overview.total_agents}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-blue-500/30">
              <div className="flex items-center gap-3">
                <Activity className="h-8 w-8 text-blue-400" />
                <div>
                  <p className="text-white/70 text-sm"> </p>
                  <p className="text-3xl font-bold text-blue-400">{overview.active_agents}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-yellow-500/30">
              <div className="flex items-center gap-3">
                <Clock className="h-8 w-8 text-yellow-400" />
                <div>
                  <p className="text-white/70 text-sm"> </p>
                  <p className="text-3xl font-bold text-yellow-400">{overview.pending_tasks}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-green-500/30">
              <div className="flex items-center gap-3">
                <FileText className="h-8 w-8 text-green-400" />
                <div>
                  <p className="text-white/70 text-sm"> </p>
                  <p className="text-3xl font-bold text-green-400">{overview.cases_being_analyzed}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-3">
                <Zap className="h-8 w-8 text-orange-400" />
                <div>
                  <p className="text-white/70 text-sm"> </p>
                  <p className="text-3xl font-bold text-white">
                    {((overview.total_tokens_today || 0) / 1000).toFixed(1)}K
                  </p>
                </div>
              </div>
            </div>
          </div>
        )}

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Active Agents */}
          <div className="bg-white/10 backdrop-blur-lg rounded-lg border border-blue-500/30 overflow-hidden">
            <div className="p-4 border-b border-white/10 bg-blue-500/10">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <Loader2 className="animate-spin text-blue-400" size={20} />
                  ({activeAgents.length})
              </h2>
            </div>
            <div className="p-4 space-y-3 max-h-[400px] overflow-y-auto">
              {activeAgents.length === 0 ? (
                <div className="text-center py-8 text-white/50">
                  <Bot className="mx-auto mb-2 opacity-50" size={32} />
                  <p>   </p>
                </div>
              ) : (
                activeAgents.map((agent, idx) => {
                  const Icon = getAgentIcon(agent.agent_type);
                  return (
                    <div
                      key={`active-${agent.agent_type}-${idx}`}
                      className="bg-blue-500/10 rounded-lg p-4 border border-blue-500/30"
                    >
                      <div className="flex items-center justify-between mb-2">
                        <div className="flex items-center gap-2">
                          <div className="p-2 rounded-lg bg-blue-500/20">
                            <Icon size={18} className="text-blue-400" />
                          </div>
                          <div>
                            <span className="text-white font-medium">{agent.agent_name}</span>
                            <p className="text-white/50 text-xs">{agent.user_email}</p>
                          </div>
                        </div>
                        <span className="text-blue-400 text-sm">
                          {agent.duration_ms ? formatDuration(agent.duration_ms) : ''}
                        </span>
                      </div>
                      {agent.current_task && (
                        <p className="text-white/70 text-sm mb-2 truncate">{agent.current_task}</p>
                      )}
                      {agent.progress !== undefined && (
                        <div className="w-full h-2 bg-white/10 rounded-full overflow-hidden">
                          <div
                            className="h-full bg-blue-400 transition-all duration-300"
                            style={{ width: `${agent.progress}%` }}
                          />
                        </div>
                      )}
                      <div className="flex items-center justify-between mt-2 text-xs text-white/40">
                        <span>{agent.case_name || agent.case_id?.slice(0, 8) || ''}</span>
                        {agent.tokens_used && (
                          <span>{agent.tokens_used.toLocaleString()} tokens</span>
                        )}
                      </div>
                    </div>
                  );
                })
              )}
            </div>
          </div>

          {/* Pending Agents */}
          <div className="bg-white/10 backdrop-blur-lg rounded-lg border border-yellow-500/30 overflow-hidden">
            <div className="p-4 border-b border-white/10 bg-yellow-500/10">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <Clock className="text-yellow-400" size={20} />
                  ({pendingAgents.length})
              </h2>
            </div>
            <div className="p-4 space-y-2 max-h-[400px] overflow-y-auto">
              {pendingAgents.length === 0 ? (
                <div className="text-center py-8 text-white/50">
                  <CheckCircle className="mx-auto mb-2 opacity-50" size={32} />
                  <p>  </p>
                </div>
              ) : (
                pendingAgents.map((agent, idx) => {
                  const Icon = getAgentIcon(agent.agent_type);
                  return (
                    <div
                      key={`pending-${agent.agent_type}-${idx}`}
                      className="flex items-center gap-3 p-3 bg-yellow-500/10 rounded-lg border border-yellow-500/20"
                    >
                      <div className="p-1.5 rounded bg-yellow-500/20">
                        <Icon size={16} className="text-yellow-400" />
                      </div>
                      <div className="flex-1 min-w-0">
                        <span className="text-white text-sm">{agent.agent_name}</span>
                        <p className="text-white/40 text-xs truncate">
                          {agent.case_name || agent.case_id?.slice(0, 12) || ''}
                        </p>
                      </div>
                      <span className="text-yellow-400/70 text-xs">{agent.user_email?.split('@')[0]}</span>
                    </div>
                  );
                })
              )}
            </div>
          </div>

          {/* Completed Agents */}
          <div className="bg-white/10 backdrop-blur-lg rounded-lg border border-green-500/30 overflow-hidden">
            <div className="p-4 border-b border-white/10 bg-green-500/10">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <CheckCircle className="text-green-400" size={20} />
                  ({completedAgents.length})
              </h2>
            </div>
            <div className="p-4 space-y-2 max-h-[400px] overflow-y-auto">
              {completedAgents.length === 0 ? (
                <div className="text-center py-8 text-white/50">
                  <Activity className="mx-auto mb-2 opacity-50" size={32} />
                  <p>  </p>
                </div>
              ) : (
                completedAgents.map((agent, idx) => {
                  const Icon = getAgentIcon(agent.agent_type);
                  return (
                    <div
                      key={`completed-${agent.agent_type}-${idx}`}
                      className="flex items-center gap-3 p-3 bg-green-500/10 rounded-lg border border-green-500/20"
                    >
                      <CheckCircle size={16} className="text-green-400" />
                      <Icon size={16} className="text-white/50" />
                      <span className="text-white text-sm flex-1">{agent.agent_name}</span>
                      {agent.tokens_used && (
                        <span className="text-white/40 text-xs">{agent.tokens_used.toLocaleString()} tok</span>
                      )}
                      {agent.duration_ms && (
                        <span className="text-green-400/70 text-xs">{formatDuration(agent.duration_ms)}</span>
                      )}
                    </div>
                  );
                })
              )}
            </div>
          </div>

          {/* Failed Agents */}
          <div className="bg-white/10 backdrop-blur-lg rounded-lg border border-red-500/30 overflow-hidden">
            <div className="p-4 border-b border-white/10 bg-red-500/10">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <XCircle className="text-red-400" size={20} />
                 ({failedAgents.length})
              </h2>
            </div>
            <div className="p-4 space-y-2 max-h-[400px] overflow-y-auto">
              {failedAgents.length === 0 ? (
                <div className="text-center py-8 text-white/50">
                  <Shield className="mx-auto mb-2 opacity-50" size={32} />
                  <p>  </p>
                </div>
              ) : (
                failedAgents.map((agent, idx) => {
                  const Icon = getAgentIcon(agent.agent_type);
                  return (
                    <div
                      key={`failed-${agent.agent_type}-${idx}`}
                      className="flex items-center gap-3 p-3 bg-red-500/10 rounded-lg border border-red-500/20"
                    >
                      <XCircle size={16} className="text-red-400" />
                      <Icon size={16} className="text-white/50" />
                      <div className="flex-1 min-w-0">
                        <span className="text-white text-sm">{agent.agent_name}</span>
                        <p className="text-red-400/70 text-xs truncate">
                          {agent.case_name || agent.case_id?.slice(0, 12) || ''}
                        </p>
                      </div>
                      <span className="text-red-400/70 text-xs">{agent.user_email?.split('@')[0]}</span>
                    </div>
                  );
                })
              )}
            </div>
          </div>
        </div>

        {/* All Available Agents (Idle) - Full width */}
        <div className="mt-6 bg-white/10 backdrop-blur-lg rounded-lg border border-white/20 overflow-hidden">
          <div className="p-4 border-b border-white/10 bg-white/5">
            <h2 className="text-xl font-bold text-white flex items-center gap-2">
              <Bot className="text-purple-400" size={20} />
                 ({idleAgents.length + activeAgents.length + pendingAgents.length})
            </h2>
            <p className="text-white/50 text-sm mt-1">37   -      </p>
          </div>
          <div className="p-4">
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-3">
              {/* Show all agents grouped by category */}
              {[...activeAgents, ...pendingAgents, ...idleAgents].map((agent, idx) => {
                const Icon = getAgentIcon(agent.agent_type);
                const isActive = agent.status === 'running';
                const isPending = agent.status === 'pending';
                const isIdle = agent.status === 'idle';

                return (
                  <div
                    key={`all-${agent.agent_type}-${idx}`}
                    className={`p-3 rounded-lg border transition-all ${
                      isActive
                        ? 'bg-blue-500/20 border-blue-500/50 shadow-lg shadow-blue-500/20'
                        : isPending
                        ? 'bg-yellow-500/10 border-yellow-500/30'
                        : 'bg-white/5 border-white/10 opacity-60 hover:opacity-100'
                    }`}
                  >
                    <div className="flex items-center gap-2 mb-2">
                      <div
                        className={`p-1.5 rounded ${
                          isActive
                            ? 'bg-blue-500/30'
                            : isPending
                            ? 'bg-yellow-500/20'
                            : 'bg-white/10'
                        }`}
                      >
                        <Icon
                          size={16}
                          className={
                            isActive
                              ? 'text-blue-400'
                              : isPending
                              ? 'text-yellow-400'
                              : 'text-white/40'
                          }
                        />
                      </div>
                      <span
                        className={`text-sm font-medium truncate ${
                          isActive
                            ? 'text-blue-300'
                            : isPending
                            ? 'text-yellow-300'
                            : 'text-white/60'
                        }`}
                      >
                        {agent.agent_name}
                      </span>
                    </div>
                    <div className="flex items-center justify-between">
                      <span
                        className={`text-xs px-2 py-0.5 rounded ${
                          isActive
                            ? 'bg-blue-500/30 text-blue-300'
                            : isPending
                            ? 'bg-yellow-500/20 text-yellow-300'
                            : 'bg-white/10 text-white/40'
                        }`}
                      >
                        {isActive ? ' ' : isPending ? ' ' : ' '}
                      </span>
                      {(agent as any).category && (
                        <span className="text-[10px] text-white/30">{(agent as any).category}</span>
                      )}
                    </div>
                    {(agent as any).description && isIdle && (
                      <p className="text-[10px] text-white/30 mt-1 line-clamp-2">
                        {(agent as any).description}
                      </p>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
        </div>

        {/* Last Update */}
        {lastUpdate && (
          <div className="mt-6 text-center">
            <p className="text-white/30 text-sm">
               : {lastUpdate.toLocaleTimeString('he-IL')}
            </p>
          </div>
        )}
      </div>
    </div>
  );
}
