/**
 * Task Status Dashboard - Background Tasks Management
 *  P1 FIX: UI for managing Celery background tasks
 */

import React, { useState, useEffect } from 'react';
import {
  Activity, CheckCircle, XCircle, Clock, Loader2, RefreshCw,
  AlertCircle, Search, X
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet } from '../../utils/api';
import { logger } from '../../utils/logger';
import { formatDateTime } from '../../utils/formatters';

interface Task {
  task_id: string;
  task_type: string;
  status: 'pending' | 'running' | 'completed' | 'failed' | 'cancelled';
  created_at: string;
  started_at?: string;
  completed_at?: string;
  progress?: number;
  result?: any;
  error?: string;
  traceback?: string;
  user_id?: string;
  case_id?: string;
}

interface TaskSummary {
  total_tasks: number;
  by_status: {
    pending: number;
    running: number;
    completed: number;
    failed: number;
    cancelled: number;
  };
  recent_failures: Task[];
}

const TaskStatusDashboard = () => {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [summary, setSummary] = useState<TaskSummary | null>(null);
  const [loading, setLoading] = useState(true);
  const [refreshing, setRefreshing] = useState(false);
  const [filter, setFilter] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedTask, setSelectedTask] = useState<Task | null>(null);

  //  PERF FIX: Only poll when page is visible
  useEffect(() => {
    loadData();

    let interval: NodeJS.Timeout | null = null;

    const startPolling = () => {
      if (!interval) {
        interval = setInterval(loadData, 30000); //  PERF: 30s instead of 5s
      }
    };

    const stopPolling = () => {
      if (interval) {
        clearInterval(interval);
        interval = null;
      }
    };

    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        loadData(); // Refresh immediately when tab becomes visible
        startPolling();
      } else {
        stopPolling();
      }
    };

    // Start polling if page is visible
    if (document.visibilityState === 'visible') {
      startPolling();
    }

    document.addEventListener('visibilitychange', handleVisibilityChange);

    return () => {
      stopPolling();
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filter]);

  const loadData = async () => {
    try {
      setRefreshing(true);
      
      const [tasksData, summaryData] = await Promise.all([
        apiGet(`/api/tasks/status${filter !== 'all' ? `?status=${filter}` : ''}`, { showErrorToast: false }),
        apiGet('/api/tasks/summary', { showErrorToast: false })
      ]);

      setTasks(tasksData.tasks || []);
      setSummary(summaryData);
    } catch (error) {
      logger.error('Failed to load tasks:', error);
      toast.error('  ');
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'completed':
        return <CheckCircle className="text-green-400" size={20} />;
      case 'failed':
        return <XCircle className="text-red-400" size={20} />;
      case 'running':
        return <Loader2 className="text-blue-400 animate-spin" size={20} />;
      case 'pending':
        return <Clock className="text-yellow-400" size={20} />;
      default:
        return <AlertCircle className="text-gray-400" size={20} />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed':
        return 'bg-green-500/20 text-green-400 border-green-500/30';
      case 'failed':
        return 'bg-red-500/20 text-red-400 border-red-500/30';
      case 'running':
        return 'bg-blue-500/20 text-blue-400 border-blue-500/30';
      case 'pending':
        return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
      default:
        return 'bg-gray-500/20 text-gray-400 border-gray-500/30';
    }
  };

  //  Using shared formatDateTime from utils/formatters

  const filteredTasks = tasks.filter(task => {
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      return (
        task.task_id.toLowerCase().includes(query) ||
        task.task_type.toLowerCase().includes(query) ||
        (task.case_id && task.case_id.toLowerCase().includes(query))
      );
    }
    return true;
  });

  if (loading && !summary) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="animate-spin text-blue-400" size={32} />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-6 border border-white/10">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-3">
            <Activity className="text-blue-400" size={32} />
            <div>
              <h2 className="text-2xl font-bold text-white">Background Tasks</h2>
              <p className="text-blue-300 text-sm">   (Celery)</p>
            </div>
          </div>
          <button
            onClick={loadData}
            disabled={refreshing}
            className="px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 text-white rounded-lg flex items-center gap-2"
          >
            <RefreshCw className={refreshing ? 'animate-spin' : ''} size={16} />
            
          </button>
        </div>

        {/* Summary Cards */}
        {summary && (
          <div className="grid grid-cols-2 md:grid-cols-5 gap-4 mt-4">
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="text-slate-400 text-sm mb-1">"</div>
              <div className="text-2xl font-bold text-white">{summary.total_tasks}</div>
            </div>
            <div className="bg-yellow-500/20 rounded-lg p-4 border border-yellow-500/30">
              <div className="text-yellow-400 text-sm mb-1"></div>
              <div className="text-2xl font-bold text-yellow-400">{summary.by_status.pending}</div>
            </div>
            <div className="bg-blue-500/20 rounded-lg p-4 border border-blue-500/30">
              <div className="text-blue-400 text-sm mb-1"></div>
              <div className="text-2xl font-bold text-blue-400">{summary.by_status.running}</div>
            </div>
            <div className="bg-green-500/20 rounded-lg p-4 border border-green-500/30">
              <div className="text-green-400 text-sm mb-1"></div>
              <div className="text-2xl font-bold text-green-400">{summary.by_status.completed}</div>
            </div>
            <div className="bg-red-500/20 rounded-lg p-4 border border-red-500/30">
              <div className="text-red-400 text-sm mb-1"></div>
              <div className="text-2xl font-bold text-red-400">{summary.by_status.failed}</div>
            </div>
          </div>
        )}
      </div>

      {/* Filters */}
      <div className="bg-slate-800 rounded-xl p-4 border border-white/10">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-slate-400" size={18} />
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="  ID, ,  case ID..."
              className="w-full pl-10 pr-4 py-2 bg-slate-700 border border-slate-600 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
          <div className="flex gap-2">
            {['all', 'pending', 'running', 'completed', 'failed'].map((status) => (
              <button
                key={status}
                onClick={() => setFilter(status)}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  filter === status
                    ? 'bg-blue-600 text-white'
                    : 'bg-slate-700 text-slate-300 hover:bg-slate-600'
                }`}
              >
                {status === 'all' ? '' : status}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Tasks Table */}
      <div className="bg-slate-800 rounded-xl border border-white/10 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-slate-700/50">
              <tr>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300">Task ID</th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300">Case ID</th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-700">
              {filteredTasks.length === 0 ? (
                <tr>
                  <td colSpan={8} className="px-4 py-8 text-center text-slate-400">
                      
                  </td>
                </tr>
              ) : (
                filteredTasks.map((task) => (
                  <tr key={task.task_id} className="hover:bg-slate-700/50">
                    <td className="px-4 py-3">
                      <div className={`inline-flex items-center gap-2 px-3 py-1 rounded-lg border ${getStatusColor(task.status)}`}>
                        {getStatusIcon(task.status)}
                        <span className="text-sm font-medium">{task.status}</span>
                      </div>
                    </td>
                    <td className="px-4 py-3 text-slate-200 text-sm">{task.task_type}</td>
                    <td className="px-4 py-3 text-slate-300 text-sm font-mono">{task.task_id.substring(0, 8)}...</td>
                    <td className="px-4 py-3 text-slate-300 text-sm font-mono">
                      {task.case_id ? task.case_id.substring(0, 8) + '...' : 'N/A'}
                    </td>
                    <td className="px-4 py-3 text-slate-400 text-sm">{formatDateTime(task.created_at)}</td>
                    <td className="px-4 py-3 text-slate-400 text-sm">{formatDateTime(task.started_at)}</td>
                    <td className="px-4 py-3 text-slate-400 text-sm">{formatDateTime(task.completed_at)}</td>
                    <td className="px-4 py-3">
                      <button
                        onClick={() => setSelectedTask(task)}
                        className="px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-sm"
                      >
                        
                      </button>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Task Details Modal */}
      {selectedTask && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-slate-800 rounded-2xl p-6 max-w-2xl w-full max-h-[90vh] overflow-y-auto border border-white/10">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-xl font-bold text-white"> </h3>
              <button
                onClick={() => setSelectedTask(null)}
                className="text-slate-400 hover:text-white"
              >
                <X size={24} />
              </button>
            </div>

            <div className="space-y-4">
              <div>
                <div className="text-slate-400 text-sm mb-1">Task ID</div>
                <div className="text-white font-mono text-sm">{selectedTask.task_id}</div>
              </div>
              <div>
                <div className="text-slate-400 text-sm mb-1"></div>
                <div className="text-white">{selectedTask.task_type}</div>
              </div>
              <div>
                <div className="text-slate-400 text-sm mb-1"></div>
                <div className={`inline-flex items-center gap-2 px-3 py-1 rounded-lg border ${getStatusColor(selectedTask.status)}`}>
                  {getStatusIcon(selectedTask.status)}
                  <span>{selectedTask.status}</span>
                </div>
              </div>
              {selectedTask.progress !== undefined && (
                <div>
                  <div className="text-slate-400 text-sm mb-1"></div>
                  <div className="w-full bg-slate-700 rounded-full h-2">
                    <div
                      className="bg-blue-500 h-2 rounded-full transition-all"
                      style={{ width: `${selectedTask.progress}%` }}
                    />
                  </div>
                  <div className="text-slate-300 text-sm mt-1">{selectedTask.progress}%</div>
                </div>
              )}
              {selectedTask.error && (
                <div>
                  <div className="text-slate-400 text-sm mb-1"></div>
                  <div className="bg-red-500/20 border border-red-500/30 rounded-lg p-3 text-red-300 text-sm">
                    {selectedTask.error}
                  </div>
                </div>
              )}
              {selectedTask.traceback && (
                <div>
                  <div className="text-slate-400 text-sm mb-1">Traceback</div>
                  <pre className="bg-slate-900 border border-slate-700 rounded-lg p-3 text-xs text-slate-300 overflow-x-auto">
                    {selectedTask.traceback}
                  </pre>
                </div>
              )}
              {selectedTask.result && (
                <div>
                  <div className="text-slate-400 text-sm mb-1"></div>
                  <pre className="bg-slate-900 border border-slate-700 rounded-lg p-3 text-xs text-slate-300 overflow-x-auto">
                    {JSON.stringify(selectedTask.result, null, 2)}
                  </pre>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default TaskStatusDashboard;

