/**
 * Metrics Dashboard - Performance Monitoring UI
 *  P1 FIX: UI for monitoring application performance
 */

import React, { useState, useEffect } from 'react';
import {
  Activity, Server, Database, Cpu, MemoryStick, RefreshCw,
  Loader2, CheckCircle, XCircle, Clock
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet } from '../../utils/api';
import { logger } from '../../utils/logger';

interface SystemMetrics {
  memory_mb: number;
  memory_percent: number;
  cpu_percent: number;
  num_threads: number;
  uptime_seconds: number;
}

interface DatabaseMetrics {
  status: string;
  latency_ms?: number;
  error?: string;
}

interface HealthResponse {
  status: string;
  timestamp: string;
  system: SystemMetrics;
  database: DatabaseMetrics;
}

const MetricsDashboard = () => {
  const [health, setHealth] = useState<HealthResponse | null>(null);
  const [loading, setLoading] = useState(true);
  const [autoRefresh, setAutoRefresh] = useState(false); //  PERF: Default off to reduce API calls

  //  PERF FIX: Increased interval from 5s to 60s, added visibility check
  useEffect(() => {
    loadHealth();

    if (autoRefresh) {
      let interval: NodeJS.Timeout | null = null;

      const poll = () => {
        if (!document.hidden) {
          loadHealth();
        }
      };

      interval = setInterval(poll, 60000); // 60 seconds instead of 5

      return () => {
        if (interval) clearInterval(interval);
      };
    }
  }, [autoRefresh]);

  const loadHealth = async () => {
    try {
      const data = await apiGet('/api/metrics/health', { showErrorToast: false });
      setHealth(data);
    } catch (error) {
      logger.error('Failed to load health metrics:', error);
      toast.error('  metrics');
    } finally {
      setLoading(false);
    }
  };

  const formatUptime = (seconds: number) => {
    const days = Math.floor(seconds / 86400);
    const hours = Math.floor((seconds % 86400) / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    return `${days}d ${hours}h ${minutes}m`;
  };

  if (loading && !health) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="animate-spin text-blue-400" size={32} />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-6 border border-white/10">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-3">
            <Activity className="text-blue-400" size={32} />
            <div>
              <h2 className="text-2xl font-bold text-white">Performance Metrics</h2>
              <p className="text-blue-300 text-sm">  </p>
            </div>
          </div>
          <div className="flex items-center gap-2">
            <label className="flex items-center gap-2 text-slate-300">
              <input
                type="checkbox"
                checked={autoRefresh}
                onChange={(e) => setAutoRefresh(e.target.checked)}
                className="rounded"
              />
               
            </label>
            <button
              onClick={loadHealth}
              disabled={loading}
              className="px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 text-white rounded-lg flex items-center gap-2"
            >
              <RefreshCw className={loading ? 'animate-spin' : ''} size={16} />
              
            </button>
          </div>
        </div>

        {/* Overall Status */}
        {health && (
          <div className={`rounded-lg p-4 border ${
            health.status === 'healthy'
              ? 'bg-green-500/20 border-green-500/30'
              : 'bg-red-500/20 border-red-500/30'
          }`}>
            <div className="flex items-center gap-3">
              {health.status === 'healthy' ? (
                <CheckCircle className="text-green-400" size={24} />
              ) : (
                <XCircle className="text-red-400" size={24} />
              )}
              <div>
                <div className="text-lg font-bold text-white">
                  Status: {health.status === 'healthy' ? 'Healthy' : 'Unhealthy'}
                </div>
                <div className="text-sm text-slate-300">
                  Last updated: {new Date(health.timestamp).toLocaleString('he-IL')}
                </div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* System Metrics */}
      {health && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
            <div className="flex items-center gap-3 mb-4">
              <MemoryStick className="text-blue-400" size={24} />
              <div className="text-slate-400 text-sm"></div>
            </div>
            <div className="text-3xl font-bold text-white mb-2">
              {health.system.memory_mb.toLocaleString()} MB
            </div>
            <div className="w-full bg-slate-700 rounded-full h-2">
              <div
                className={`h-2 rounded-full ${
                  health.system.memory_percent > 80 ? 'bg-red-500' :
                  health.system.memory_percent > 60 ? 'bg-yellow-500' :
                  'bg-green-500'
                }`}
                style={{ width: `${Math.min(health.system.memory_percent, 100)}%` }}
              />
            </div>
            <div className="text-sm text-slate-400 mt-2">
              {health.system.memory_percent.toFixed(1)}% 
            </div>
          </div>

          <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
            <div className="flex items-center gap-3 mb-4">
              <Cpu className="text-green-400" size={24} />
              <div className="text-slate-400 text-sm">CPU</div>
            </div>
            <div className="text-3xl font-bold text-white mb-2">
              {health.system.cpu_percent.toFixed(1)}%
            </div>
            <div className="w-full bg-slate-700 rounded-full h-2">
              <div
                className={`h-2 rounded-full ${
                  health.system.cpu_percent > 80 ? 'bg-red-500' :
                  health.system.cpu_percent > 60 ? 'bg-yellow-500' :
                  'bg-green-500'
                }`}
                style={{ width: `${Math.min(health.system.cpu_percent, 100)}%` }}
              />
            </div>
            <div className="text-sm text-slate-400 mt-2">
               
            </div>
          </div>

          <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
            <div className="flex items-center gap-3 mb-4">
              <Server className="text-purple-400" size={24} />
              <div className="text-slate-400 text-sm">Threads</div>
            </div>
            <div className="text-3xl font-bold text-white">
              {health.system.num_threads}
            </div>
            <div className="text-sm text-slate-400 mt-2">
               
            </div>
          </div>

          <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
            <div className="flex items-center gap-3 mb-4">
              <Clock className="text-yellow-400" size={24} />
              <div className="text-slate-400 text-sm">Uptime</div>
            </div>
            <div className="text-2xl font-bold text-white">
              {formatUptime(health.system.uptime_seconds)}
            </div>
            <div className="text-sm text-slate-400 mt-2">
               
            </div>
          </div>
        </div>
      )}

      {/* Database Metrics */}
      {health && (
        <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
          <h3 className="text-xl font-bold text-white mb-4 flex items-center gap-2">
            <Database className="text-blue-400" size={24} />
            Database Status
          </h3>
          <div className={`rounded-lg p-4 border ${
            health.database.status === 'healthy'
              ? 'bg-green-500/20 border-green-500/30'
              : 'bg-red-500/20 border-red-500/30'
          }`}>
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                {health.database.status === 'healthy' ? (
                  <CheckCircle className="text-green-400" size={24} />
                ) : (
                  <XCircle className="text-red-400" size={24} />
                )}
                <div>
                  <div className="text-lg font-bold text-white">
                    {health.database.status === 'healthy' ? 'Connected' : 'Disconnected'}
                  </div>
                  {health.database.latency_ms !== undefined && (
                    <div className="text-sm text-slate-300">
                      Latency: {health.database.latency_ms}ms
                    </div>
                  )}
                  {health.database.error && (
                    <div className="text-sm text-red-300 mt-1">
                      Error: {health.database.error}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Prometheus Link */}
      <div className="bg-blue-900/20 border border-blue-500/30 rounded-lg p-4">
        <p className="text-blue-300 text-sm">
          <Activity className="inline mr-2" size={16} />
          Prometheus metrics available at: <code className="bg-slate-800 px-2 py-1 rounded">/api/metrics</code>
        </p>
      </div>
    </div>
  );
};

export default MetricsDashboard;

