/**
 * Court Decisions Dashboard - Import     
 *
 * Features:
 * - Import court decisions via Apify Puppeteer scraper
 * - Bi-monthly incremental sync
 * - View import statistics by court, year, judge
 * - Monitor import progress in real-time
 */

import React, { useState, useEffect, useCallback, useRef } from 'react';
import {
  Gavel, Download, RefreshCw, BarChart3, Calendar, Users,
  CheckCircle, AlertTriangle, XCircle, Loader2, Play, Pause,
  Clock, Database, FileText, Building, TrendingUp, Upload,
  Zap, Search
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPost, apiDelete, apiUpload } from '../../utils/api';
import { logger } from '../../utils/logger';

// Types
interface ImportStats {
  total_rulings: number;
  by_court_type: Record<string, number>;
  by_year: Record<string, number>;
  top_judges: Record<string, number>;
  last_sync: string | null;
  apify_configured: boolean;
}

interface ImportStatus {
  job_id: string | null;
  status: 'idle' | 'running' | 'completed' | 'failed';
  progress?: {
    current: number;
    total: number;
    imported?: number;
    type?: string;
    stage?: string;
    current_file?: string;
    accepted?: number;
    duplicates?: number;
    indexed?: number;
    analyzed?: number;
    errors?: number;
  };
  started_at?: string;
  completed_at?: string;
  stats?: {
    imported: number;
    duplicates: number;
    errors: number;
  };
  error?: string;
}

interface RulingListItem {
  id: string;
  case_number: string;
  court: string;
  court_type?: string | null;
  ruling_type?: string | null;
  ruling_date?: string | null;
  judge_names: string[];
  case_name?: string | null;
  source_type: string;
  source_url?: string | null;
  text_length?: number | null;
  created_at?: string | null;
  is_indexed: boolean;
  indexed_at?: string | null;
  is_analyzed: boolean;
  is_processed: boolean;
  processed_at?: string | null;
  processing_error?: string | null;
  ready_for_legal_knowledge: boolean;
}

interface RulingsListResponse {
  total: number;
  limit: number;
  offset: number;
  items: RulingListItem[];
}

const COURT_TYPE_LABELS: Record<string, string> = {
  'supreme': '  ',
  'district': '  ',
  'magistrate': '  ',
  'labor': '  ',
  'family': '   ',
  'traffic': '  ',
  'juvenile': '  ',
  'unknown': ''
};

const CourtDecisionsDashboard: React.FC = () => {
  const [stats, setStats] = useState<ImportStats | null>(null);
  const [status, setStatus] = useState<ImportStatus>({ job_id: null, status: 'idle' });
  const [loading, setLoading] = useState(true);
  const [importing, setImporting] = useState(false);
  const [syncing, setSyncing] = useState(false);
  const [uploading, setUploading] = useState(false);
  const [analyzing, setAnalyzing] = useState(false);
  const [selectedSource, setSelectedSource] = useState<'spokesperson' | 'supreme'>('spokesperson');
  const [maxDecisions, setMaxDecisions] = useState(5000);
  const [selectedCourtType, setSelectedCourtType] = useState('');
  const [autoAnalyze, setAutoAnalyze] = useState(false);  //  P0: OFF by default
  const [autoIndex, setAutoIndex] = useState(true);
  const [activeTab, setActiveTab] = useState<'import' | 'analyze'>('import');  //  P0: Tab state
  const [pendingCount, setPendingCount] = useState(0);
  const [analysisLimit, setAnalysisLimit] = useState(500);
  const [bulkJudgmentsMax, setBulkJudgmentsMax] = useState(1000);
  const [bulkJudgmentsCourtType, setBulkJudgmentsCourtType] = useState('');
  const [bulkJudgmentsAutoIndex, setBulkJudgmentsAutoIndex] = useState(true);
  const [bulkJudgmentsAutoAnalyze, setBulkJudgmentsAutoAnalyze] = useState(false);
  const [bulkJudgmentsUploading, setBulkJudgmentsUploading] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const bulkJudgmentsInputRef = useRef<HTMLInputElement>(null);

  // Rulings management
  const [rulingsLoading, setRulingsLoading] = useState(false);
  const [rulings, setRulings] = useState<RulingListItem[]>([]);
  const [rulingsTotal, setRulingsTotal] = useState(0);
  const [rulingsQuery, setRulingsQuery] = useState('');
  const [rulingsQueryApplied, setRulingsQueryApplied] = useState('');
  const [rulingsStatus, setRulingsStatus] = useState<'all' | 'ready' | 'needs_index' | 'needs_analysis' | 'errored'>('needs_index');
  const [rulingsOffset, setRulingsOffset] = useState(0);
  const rulingsLimit = 50;

  // Fetch stats
  const fetchStats = useCallback(async () => {
    try {
      const data = await apiGet<ImportStats>('/api/admin/court-decisions/stats');
      setStats(data);
    } catch (error) {
      logger.error('Error fetching court decisions stats:', error);
    }
  }, []);

  // Fetch current job status
  const fetchStatus = useCallback(async () => {
    try {
      const data = await apiGet<ImportStatus>('/api/admin/court-decisions/status');
      setStatus(data);

      // If job is running, keep polling
      if (data.status === 'running') {
        setImporting(true);
      } else {
        setImporting(false);
        setSyncing(false);
      }
    } catch (error) {
      logger.error('Error fetching import status:', error);
    }
  }, []);

  const fetchRulings = useCallback(async () => {
    try {
      setRulingsLoading(true);
      const params = new URLSearchParams();
      params.set('status', rulingsStatus);
      params.set('limit', String(rulingsLimit));
      params.set('offset', String(rulingsOffset));
      if (rulingsQueryApplied.trim()) params.set('q', rulingsQueryApplied.trim());
      const data = await apiGet<RulingsListResponse>(`/api/admin/court-decisions/rulings?${params.toString()}`);
      setRulings(data.items || []);
      setRulingsTotal(data.total || 0);
    } catch (error) {
      logger.error('Error fetching rulings list:', error);
      toast.error('    ');
    } finally {
      setRulingsLoading(false);
    }
  }, [rulingsQueryApplied, rulingsStatus, rulingsOffset]);

  // Initial load
  useEffect(() => {
    const loadData = async () => {
      setLoading(true);
      await Promise.all([fetchStats(), fetchStatus()]);
      setLoading(false);
    };
    loadData();
  }, [fetchStats, fetchStatus]);

  // Auto-refresh list on paging/filter changes (but not on every keystroke)
  useEffect(() => {
    fetchRulings();
  }, [fetchRulings]);

  // Poll status while importing
  useEffect(() => {
    let interval: NodeJS.Timeout | null = null;

    if (status.status === 'running') {
      interval = setInterval(fetchStatus, 3000);
    }

    return () => {
      if (interval) clearInterval(interval);
    };
  }, [status.status, fetchStatus]);

  // Start full import
  const startImport = async () => {
    if (!stats?.apify_configured) {
      toast.error('Apify  .  APIFY_API_TOKEN.');
      return;
    }

    try {
      setImporting(true);
      await apiPost('/api/admin/court-decisions/import', {
        source: selectedSource,
        max_decisions: maxDecisions
      });
      toast.success(' .    .');
      await fetchStatus();
    } catch (error: any) {
      logger.error('Error starting import:', error);
      toast.error(error.message || '  ');
      setImporting(false);
    }
  };

  // Start incremental sync
  const startSync = async () => {
    if (!stats?.apify_configured) {
      toast.error('Apify  .  APIFY_API_TOKEN.');
      return;
    }

    try {
      setSyncing(true);
      await apiPost('/api/admin/court-decisions/sync', {
        days_back: 60
      });
      toast.success(' .    .');
      await fetchStatus();
    } catch (error: any) {
      logger.error('Error starting sync:', error);
      toast.error(error.message || '  ');
      setSyncing(false);
    }
  };

  // Reset stuck job
  const resetJob = async () => {
    try {
      await apiDelete('/api/admin/court-decisions/reset-job');
      toast.success('  ');
      await fetchStatus();
    } catch (error: any) {
      toast.error(error.message || ' ');
    }
  };

  // Fetch pending analysis count
  const fetchPendingCount = useCallback(async () => {
    try {
      const data = await apiGet<{ pending: number }>('/api/admin/court-decisions/pending-count');
      setPendingCount(data.pending || 0);
    } catch (error) {
      logger.error('Error fetching pending count:', error);
    }
  }, []);

  // Upload bulk files
  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files;
    if (!files || files.length === 0) return;

    const formData = new FormData();
    Array.from(files).forEach(file => {
      formData.append('files', file);
    });
    formData.append('court_type', selectedCourtType);
    formData.append('auto_analyze', String(autoAnalyze));
    formData.append('auto_index', String(autoIndex));

    try {
      setUploading(true);
      await apiUpload('/api/admin/court-decisions/upload-bulk', formData, { timeoutMs: 600000 });

      toast.success(`  ${files.length} `);
      await fetchStatus();
    } catch (error: any) {
      logger.error('Error uploading files:', error);
      toast.error(error.message || ' ');
      setUploading(false);
    }

    // Reset file input
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  // Start analysis of pending rulings
  const startAnalysis = async () => {
    try {
      setAnalyzing(true);
      await apiPost(`/api/admin/court-decisions/analyze?limit=${encodeURIComponent(String(analysisLimit))}`, {});
      toast.success(' .    .');
      await fetchStatus();
    } catch (error: any) {
      logger.error('Error starting analysis:', error);
      toast.error(error.message || '  ');
      setAnalyzing(false);
    }
  };

  // Upload a single TXT containing many judgments (JSONL or delimiter-based)
  const handleBulkJudgmentsUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const maxItems = Number(bulkJudgmentsMax || 1000);
    if (!Number.isFinite(maxItems) || maxItems < 1 || maxItems > 10000) {
      toast.error('    1 -10,000');
      return;
    }

    const formData = new FormData();
    formData.append('file', file);
    formData.append('court_type', bulkJudgmentsCourtType);
    formData.append('max_judgments', String(maxItems));
    formData.append('auto_index', String(bulkJudgmentsAutoIndex));
    formData.append('auto_analyze', String(bulkJudgmentsAutoAnalyze));

    try {
      setBulkJudgmentsUploading(true);
      await apiUpload('/api/admin/court-decisions/upload-judgments-txt', formData, { timeoutMs: 600000 });
      toast.success(` .    ${maxItems.toLocaleString()}  `);
      await fetchStatus();
    } catch (error: any) {
      logger.error('Error uploading bulk judgments txt:', error);
      toast.error(error.message || '  TXT ');
      setBulkJudgmentsUploading(false);
    } finally {
      if (bulkJudgmentsInputRef.current) bulkJudgmentsInputRef.current.value = '';
    }
  };

  // Also fetch pending count on load
  useEffect(() => {
    fetchPendingCount();
  }, [fetchPendingCount]);

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="w-8 h-8 text-blue-400 animate-spin" />
      </div>
    );
  }

  return (
    <div className="space-y-6" dir="rtl">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="w-12 h-12 bg-amber-500/20 rounded-xl flex items-center justify-center">
            <Gavel className="w-6 h-6 text-amber-400" />
          </div>
          <div>
            <h2 className="text-xl font-bold text-white">  </h2>
            <p className="text-sm text-white/60">  </p>
          </div>
        </div>
        <button
          onClick={() => { fetchStats(); fetchStatus(); fetchRulings(); }}
          className="p-2 bg-white/10 hover:bg-white/20 rounded-lg transition-colors"
          title=""
        >
          <RefreshCw className="w-5 h-5 text-white/70" />
        </button>
      </div>

      {/* Apify Configuration Warning */}
      {!stats?.apify_configured && (
        <div className="bg-orange-500/20 border border-orange-500/50 rounded-xl p-4 flex items-start gap-3">
          <AlertTriangle className="w-5 h-5 text-orange-400 mt-0.5" />
          <div>
            <h3 className="font-semibold text-orange-300">Apify  </h3>
            <p className="text-sm text-orange-200/80 mt-1">
                  <code className="bg-black/30 px-1 rounded">APIFY_API_TOKEN</code>     .
            </p>
          </div>
        </div>
      )}

      {/*  P0: Tab Navigation - Import / Analyze */}
      <div className="flex border-b border-white/10">
        <button
          onClick={() => setActiveTab('import')}
          className={`flex-1 px-6 py-3 font-medium text-sm transition-colors flex items-center justify-center gap-2 ${
            activeTab === 'import'
              ? 'text-blue-400 border-b-2 border-blue-400 bg-blue-500/10'
              : 'text-white/60 hover:text-white/80 hover:bg-white/5'
          }`}
        >
          <Upload className="w-4 h-4" />
           
        </button>
        <button
          onClick={() => setActiveTab('analyze')}
          className={`flex-1 px-6 py-3 font-medium text-sm transition-colors flex items-center justify-center gap-2 ${
            activeTab === 'analyze'
              ? 'text-amber-400 border-b-2 border-amber-400 bg-amber-500/10'
              : 'text-white/60 hover:text-white/80 hover:bg-white/5'
          }`}
        >
          <Zap className="w-4 h-4" />
           
          {pendingCount > 0 && (
            <span className="px-2 py-0.5 bg-amber-500/30 text-amber-300 rounded-full text-xs">
              {pendingCount}
            </span>
          )}
        </button>
      </div>

      {/* Tab Descriptions */}
      <div className="text-sm text-white/60 px-1">
        {activeTab === 'import' ? (
          <p> = OCR +   +  /. <span className="text-green-400">  .</span></p>
        ) : (
          <p> =   (ArgumentOutcome), <span className="text-amber-400">  .</span>   .</p>
        )}
      </div>

      {/*  P0: Conditional Content based on Tab */}
      {activeTab === 'import' && (
        <>
      {/* Stats Cards */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <div className="bg-white/10 rounded-xl p-4">
          <div className="flex items-center gap-2 text-blue-400 mb-2">
            <Database className="w-5 h-5" />
            <span className="text-sm">"  </span>
          </div>
          <div className="text-2xl font-bold text-white">
            {(stats?.total_rulings || 0).toLocaleString()}
          </div>
        </div>

        <div className="bg-white/10 rounded-xl p-4">
          <div className="flex items-center gap-2 text-green-400 mb-2">
            <Building className="w-5 h-5" />
            <span className="text-sm">  </span>
          </div>
          <div className="text-2xl font-bold text-white">
            {Object.keys(stats?.by_court_type || {}).length}
          </div>
        </div>

        <div className="bg-white/10 rounded-xl p-4">
          <div className="flex items-center gap-2 text-purple-400 mb-2">
            <Users className="w-5 h-5" />
            <span className="text-sm"> </span>
          </div>
          <div className="text-2xl font-bold text-white">
            {Object.keys(stats?.top_judges || {}).length}
          </div>
        </div>

        <div className="bg-white/10 rounded-xl p-4">
          <div className="flex items-center gap-2 text-amber-400 mb-2">
            <Clock className="w-5 h-5" />
            <span className="text-sm"> </span>
          </div>
          <div className="text-lg font-bold text-white">
            {stats?.last_sync
              ? new Date(stats.last_sync).toLocaleDateString('he-IL')
              : ' '
            }
          </div>
        </div>
      </div>

      {/* Import Controls */}
      <div className="bg-white/10 rounded-xl p-6">
        <h3 className="text-lg font-semibold text-white mb-4"> </h3>

        <div className="grid md:grid-cols-3 gap-6">
          {/* Full Import */}
          <div className="bg-black/20 rounded-xl p-4">
            <h4 className="font-medium text-white mb-3 flex items-center gap-2">
              <Download className="w-4 h-4 text-blue-400" />
               
            </h4>

            <div className="space-y-3 mb-4">
              <div>
                <label className="block text-sm text-white/70 mb-1">:</label>
                <select
                  value={selectedSource}
                  onChange={(e) => setSelectedSource(e.target.value as 'spokesperson' | 'supreme')}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                  disabled={importing}
                >
                  <option value="spokesperson">  </option>
                  <option value="supreme">  </option>
                </select>
              </div>

              <div>
                <label className="block text-sm text-white/70 mb-1">  :</label>
                <input
                  type="number"
                  value={maxDecisions}
                  onChange={(e) => setMaxDecisions(parseInt(e.target.value) || 5000)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                  min={100}
                  max={15000}
                  disabled={importing}
                />
              </div>
            </div>

            <button
              onClick={startImport}
              disabled={importing || syncing || !stats?.apify_configured}
              className="w-full px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600 disabled:cursor-not-allowed text-white rounded-lg font-medium flex items-center justify-center gap-2 transition-colors"
            >
              {importing ? (
                <>
                  <Loader2 className="w-4 h-4 animate-spin" />
                  ...
                </>
              ) : (
                <>
                  <Play className="w-4 h-4" />
                   
                </>
              )}
            </button>
          </div>

          {/* Incremental Sync */}
          <div className="bg-black/20 rounded-xl p-4">
            <h4 className="font-medium text-white mb-3 flex items-center gap-2">
              <RefreshCw className="w-4 h-4 text-green-400" />
               -
            </h4>

            <p className="text-sm text-white/60 mb-4">
                     .
                 .
            </p>

            <button
              onClick={startSync}
              disabled={importing || syncing || !stats?.apify_configured}
              className="w-full px-4 py-2 bg-green-600 hover:bg-green-700 disabled:bg-gray-600 disabled:cursor-not-allowed text-white rounded-lg font-medium flex items-center justify-center gap-2 transition-colors"
            >
              {syncing ? (
                <>
                  <Loader2 className="w-4 h-4 animate-spin" />
                  ...
                </>
              ) : (
                <>
                  <RefreshCw className="w-4 h-4" />
                   
                </>
              )}
            </button>
          </div>

          {/* Bulk PDF Upload */}
          <div className="bg-black/20 rounded-xl p-4">
            <h4 className="font-medium text-white mb-3 flex items-center gap-2">
              <Upload className="w-4 h-4 text-purple-400" />
                
            </h4>

            <div className="space-y-3 mb-4">
              <div>
                <label className="block text-sm text-white/70 mb-1">  :</label>
                <select
                  value={selectedCourtType}
                  onChange={(e) => setSelectedCourtType(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                  disabled={uploading}
                >
                  <option value="">  </option>
                  <option value="">  </option>
                  <option value="">  </option>
                  <option value="">  </option>
                  <option value="">   </option>
                  <option value=""></option>
                </select>
              </div>

              <label className="flex items-center gap-2 text-sm text-white/70 cursor-pointer">
                <input
                  type="checkbox"
                  checked={autoAnalyze}
                  onChange={(e) => setAutoAnalyze(e.target.checked)}
                  className="w-4 h-4 rounded bg-white/10 border-white/20"
                  disabled={uploading}
                />
                   
              </label>

              <label className="flex items-center gap-2 text-sm text-white/70 cursor-pointer">
                <input
                  type="checkbox"
                  checked={autoIndex}
                  onChange={(e) => setAutoIndex(e.target.checked)}
                  className="w-4 h-4 rounded bg-white/10 border-white/20"
                  disabled={uploading}
                />
                 Normalize + Index  
              </label>
            </div>

            <input
              ref={fileInputRef}
              type="file"
              multiple
              accept=".pdf,.txt,.docx,.doc"
              onChange={handleFileUpload}
              className="hidden"
            />

            <button
              onClick={() => fileInputRef.current?.click()}
              disabled={uploading || importing || syncing}
              className="w-full px-4 py-2 bg-purple-600 hover:bg-purple-700 disabled:bg-gray-600 disabled:cursor-not-allowed text-white rounded-lg font-medium flex items-center justify-center gap-2 transition-colors"
            >
              {uploading ? (
                <>
                  <Loader2 className="w-4 h-4 animate-spin" />
                  ...
                </>
              ) : (
                <>
                  <Upload className="w-4 h-4" />
                    ( 100)
                </>
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Bulk TXT Judgments Upload (up to 10,000 in one file) */}
      <div className="bg-white/10 rounded-xl p-6">
        <h3 className="text-lg font-semibold text-white mb-4"> TXT  ( 10,000)</h3>
        <div className="grid md:grid-cols-3 gap-6">
          <div className="bg-black/20 rounded-xl p-4 md:col-span-2">
            <p className="text-sm text-white/60 mb-3">
                <strong>JSONL</strong> (  = JSON  <code className="bg-black/30 px-1 rounded">text</code>),
                  <code className="bg-black/30 px-1 rounded">---</code>.
            </p>
            <div className="grid md:grid-cols-3 gap-3">
              <div>
                <label className="block text-sm text-white/70 mb-1">  :</label>
                <select
                  value={bulkJudgmentsCourtType}
                  onChange={(e) => setBulkJudgmentsCourtType(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                  disabled={bulkJudgmentsUploading}
                >
                  <option value=""></option>
                  <option value="">  </option>
                  <option value="">  </option>
                  <option value="">  </option>
                  <option value="">  </option>
                  <option value="">   </option>
                </select>
              </div>
              <div>
                <label className="block text-sm text-white/70 mb-1">  (110,000):</label>
                <input
                  type="number"
                  min={1}
                  max={10000}
                  value={bulkJudgmentsMax}
                  onChange={(e) => setBulkJudgmentsMax(Number(e.target.value) || 1)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                  disabled={bulkJudgmentsUploading}
                />
                <div className="flex flex-wrap gap-2 mt-2">
                  {[100, 500, 1000, 10000].map((n) => (
                    <button
                      key={n}
                      type="button"
                      onClick={() => setBulkJudgmentsMax(n)}
                      className="px-2 py-1 text-xs rounded-lg bg-white/10 hover:bg-white/20 border border-white/10 text-white/80"
                      disabled={bulkJudgmentsUploading}
                    >
                      {n.toLocaleString()}
                    </button>
                  ))}
                </div>
              </div>
              <div className="flex flex-col justify-end gap-2">
                <label className="flex items-center gap-2 text-sm text-white/70 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={bulkJudgmentsAutoIndex}
                    onChange={(e) => setBulkJudgmentsAutoIndex(e.target.checked)}
                    className="w-4 h-4 rounded bg-white/10 border-white/20"
                    disabled={bulkJudgmentsUploading}
                  />
                  Normalize + Index
                </label>
                <label className="flex items-center gap-2 text-sm text-white/70 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={bulkJudgmentsAutoAnalyze}
                    onChange={(e) => setBulkJudgmentsAutoAnalyze(e.target.checked)}
                    className="w-4 h-4 rounded bg-white/10 border-white/20"
                    disabled={bulkJudgmentsUploading}
                  />
                   
                </label>
              </div>
            </div>

            <input
              ref={bulkJudgmentsInputRef}
              type="file"
              accept=".txt,.jsonl"
              onChange={handleBulkJudgmentsUpload}
              className="hidden"
            />
            <div className="mt-4">
              <button
                onClick={() => bulkJudgmentsInputRef.current?.click()}
                disabled={bulkJudgmentsUploading || importing || syncing || uploading}
                className="w-full px-4 py-2 bg-indigo-600 hover:bg-indigo-700 disabled:bg-gray-600 disabled:cursor-not-allowed text-white rounded-lg font-medium flex items-center justify-center gap-2 transition-colors"
              >
                {bulkJudgmentsUploading ? (
                  <>
                    <Loader2 className="w-4 h-4 animate-spin" />
                    ...
                  </>
                ) : (
                  <>
                    <Upload className="w-4 h-4" />
                      TXT ( 10,000)
                  </>
                )}
              </button>
            </div>
          </div>
          <div className="bg-black/20 rounded-xl p-4">
            <h4 className="font-medium text-white mb-2">   ?</h4>
            <ul className="text-sm text-white/70 space-y-1">
              <li>   </li>
              <li> Normalize (  + )</li>
              <li> Index (FTS5/  )</li>
              <li>   (Analytics)  </li>
            </ul>
          </div>
        </div>
      </div>

      {/* Rulings Management */}
      <div className="bg-white/10 rounded-xl p-6">
        <div className="flex items-center justify-between gap-3">
          <h3 className="text-lg font-semibold text-white">   (  )</h3>
          <div className="text-sm text-white/60">: {rulingsTotal.toLocaleString()}</div>
        </div>

        <div className="grid md:grid-cols-3 gap-3 mt-4">
          <div className="md:col-span-2">
            <div className="flex items-center gap-2 bg-black/20 border border-white/10 rounded-xl px-3 py-2">
              <Search className="w-4 h-4 text-white/50" />
              <input
                value={rulingsQuery}
                onChange={(e) => { setRulingsQuery(e.target.value); setRulingsOffset(0); }}
                placeholder="    /   /   / ..."
                className="w-full bg-transparent outline-none text-sm text-white placeholder:text-white/40"
              />
              <button
                onClick={() => {
                  setRulingsQueryApplied(rulingsQuery);
                  setRulingsOffset(0);
                }}
                className="px-3 py-1.5 rounded-lg bg-white/10 hover:bg-white/20 text-white/80 text-sm"
                disabled={rulingsLoading}
              >
                
              </button>
            </div>
          </div>
          <div>
            <select
              value={rulingsStatus}
              onChange={(e) => { setRulingsStatus(e.target.value as any); setRulingsOffset(0); }}
              className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
              disabled={rulingsLoading}
              title="   "
            >
              <option value="needs_index"> </option>
              <option value="needs_analysis"> </option>
              <option value="ready">  </option>
              <option value="errored"></option>
              <option value="all"></option>
            </select>
          </div>
        </div>

        <div className="mt-4 overflow-x-auto">
          <table className="min-w-full text-sm">
            <thead>
              <tr className="text-white/70 border-b border-white/10">
                <th className="text-right py-2 px-2"></th>
                <th className="text-right py-2 px-2"> </th>
                <th className="text-right py-2 px-2"></th>
                <th className="text-right py-2 px-2"></th>
                <th className="text-right py-2 px-2"></th>
                <th className="text-right py-2 px-2"></th>
              </tr>
            </thead>
            <tbody>
              {rulingsLoading ? (
                <tr>
                  <td colSpan={6} className="py-6 text-center text-white/60">
                    <Loader2 className="w-5 h-5 animate-spin inline-block mr-2" />
                    ...
                  </td>
                </tr>
              ) : rulings.length === 0 ? (
                <tr>
                  <td colSpan={6} className="py-6 text-center text-white/50">
                     
                  </td>
                </tr>
              ) : (
                rulings.map((r) => (
                  <tr key={r.id} className="border-b border-white/5 text-white/80">
                    <td className="py-2 px-2">
                      <div className="font-medium text-white">{r.case_number}</div>
                      {r.case_name && <div className="text-xs text-white/60 line-clamp-1">{r.case_name}</div>}
                      {r.processing_error && <div className="text-xs text-red-300 mt-1 line-clamp-1">{r.processing_error}</div>}
                    </td>
                    <td className="py-2 px-2">
                      <div>{r.court}</div>
                      {r.court_type && <div className="text-xs text-white/60">{r.court_type}</div>}
                    </td>
                    <td className="py-2 px-2">
                      {r.is_indexed ? (
                        <span className="inline-flex items-center gap-1 text-green-300">
                          <CheckCircle className="w-4 h-4" /> 
                        </span>
                      ) : (
                        <span className="inline-flex items-center gap-1 text-amber-300">
                          <AlertTriangle className="w-4 h-4" /> 
                        </span>
                      )}
                    </td>
                    <td className="py-2 px-2">
                      {r.is_analyzed ? (
                        <span className="inline-flex items-center gap-1 text-green-300">
                          <CheckCircle className="w-4 h-4" /> 
                        </span>
                      ) : (
                        <span className="inline-flex items-center gap-1 text-white/50">
                          <Clock className="w-4 h-4" /> 
                        </span>
                      )}
                    </td>
                    <td className="py-2 px-2">
                      {r.ready_for_legal_knowledge ? (
                        <span className="inline-flex items-center gap-1 text-green-300">
                          <CheckCircle className="w-4 h-4" /> 
                        </span>
                      ) : (
                        <span className="inline-flex items-center gap-1 text-white/50">
                          <XCircle className="w-4 h-4" /> 
                        </span>
                      )}
                    </td>
                    <td className="py-2 px-2 text-xs text-white/60">
                      {r.created_at ? new Date(r.created_at).toLocaleString('he-IL') : ''}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>

        <div className="mt-4 flex items-center justify-between">
          <div className="text-xs text-white/50">
             {rulingsTotal === 0 ? 0 : rulingsOffset + 1}{Math.min(rulingsOffset + rulingsLimit, rulingsTotal)}  {rulingsTotal.toLocaleString()}
          </div>
          <div className="flex items-center gap-2">
            <button
              onClick={() => { setRulingsOffset(Math.max(0, rulingsOffset - rulingsLimit)); }}
              disabled={rulingsOffset === 0 || rulingsLoading}
              className="px-3 py-1.5 rounded-lg bg-white/10 hover:bg-white/20 disabled:opacity-50 text-white/80 text-sm"
            >
              
            </button>
            <button
              onClick={() => { setRulingsOffset(rulingsOffset + rulingsLimit); }}
              disabled={rulingsOffset + rulingsLimit >= rulingsTotal || rulingsLoading}
              className="px-3 py-1.5 rounded-lg bg-white/10 hover:bg-white/20 disabled:opacity-50 text-white/80 text-sm"
            >
              
            </button>
          </div>
        </div>
      </div>
        </>
      )}

      {/*  P0: Analyze Tab Content */}
      {activeTab === 'analyze' && (
        <>
          {/* Analysis Stats */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="bg-white/10 rounded-xl p-4">
              <div className="flex items-center gap-2 text-amber-400 mb-2">
                <Clock className="w-5 h-5" />
                <span className="text-sm"> </span>
              </div>
              <div className="text-2xl font-bold text-white">
                {pendingCount.toLocaleString()}
              </div>
            </div>

            <div className="bg-white/10 rounded-xl p-4">
              <div className="flex items-center gap-2 text-green-400 mb-2">
                <CheckCircle className="w-5 h-5" />
                <span className="text-sm"></span>
              </div>
              <div className="text-2xl font-bold text-white">
                {((stats?.total_rulings || 0) - pendingCount).toLocaleString()}
              </div>
            </div>

            <div className="bg-white/10 rounded-xl p-4">
              <div className="flex items-center gap-2 text-blue-400 mb-2">
                <Database className="w-5 h-5" />
                <span className="text-sm">" </span>
              </div>
              <div className="text-2xl font-bold text-white">
                {(stats?.total_rulings || 0).toLocaleString()}
              </div>
            </div>

            <div className="bg-white/10 rounded-xl p-4">
              <div className="flex items-center gap-2 text-purple-400 mb-2">
                <TrendingUp className="w-5 h-5" />
                <span className="text-sm"> </span>
              </div>
              <div className="text-2xl font-bold text-white">
                {stats?.total_rulings ? Math.round(((stats.total_rulings - pendingCount) / stats.total_rulings) * 100) : 0}%
              </div>
            </div>
          </div>

          {/* Analysis Controls */}
          <div className="bg-amber-500/20 border border-amber-500/50 rounded-xl p-6">
            <div className="flex items-center gap-3 mb-4">
              <Zap className="w-6 h-6 text-amber-400" />
              <div>
                <h3 className="font-semibold text-amber-300 text-lg">  </h3>
                <p className="text-sm text-amber-200/80">
                   :  ArgumentOutcome,  ,  
                </p>
              </div>
            </div>

            <div className="grid md:grid-cols-4 gap-4 mb-4">
              <div>
                <label className="block text-sm text-amber-200/70 mb-1"> :</label>
                <select
                  value={analysisLimit}
                  onChange={(e) => setAnalysisLimit(parseInt(e.target.value, 10) || 500)}
                  className="w-full px-3 py-2 bg-black/30 border border-amber-500/30 rounded-lg text-white"
                  disabled={analyzing || importing || syncing || uploading}
                >
                  <option value={100}>100</option>
                  <option value={500}>500</option>
                  <option value={1000}>1,000</option>
                  <option value={5000}>5,000</option>
                  <option value={10000}>10,000</option>
                </select>
              </div>

              <div>
                <label className="block text-sm text-amber-200/70 mb-1">  :</label>
                <select
                  className="w-full px-3 py-2 bg-black/30 border border-amber-500/30 rounded-lg text-white"
                  disabled={analyzing || importing || syncing || uploading}
                  defaultValue=""
                >
                  <option value=""></option>
                  <option value="supreme">  </option>
                  <option value="district"></option>
                  <option value="magistrate"></option>
                  <option value="labor"></option>
                  <option value="family"></option>
                </select>
              </div>

              <div>
                <label className="block text-sm text-amber-200/70 mb-1">:</label>
                <input
                  type="date"
                  className="w-full px-3 py-2 bg-black/30 border border-amber-500/30 rounded-lg text-white"
                  disabled={analyzing || importing || syncing || uploading}
                />
              </div>

              <div className="flex items-end">
                <button
                  onClick={startAnalysis}
                  disabled={analyzing || importing || syncing || uploading || pendingCount === 0}
                  className="w-full px-4 py-2 bg-amber-600 hover:bg-amber-700 disabled:bg-gray-600 disabled:cursor-not-allowed text-white rounded-lg font-medium flex items-center justify-center gap-2 transition-colors"
                >
                  {analyzing ? (
                    <>
                      <Loader2 className="w-4 h-4 animate-spin" />
                      ...
                    </>
                  ) : (
                    <>
                      <Zap className="w-4 h-4" />
                       
                    </>
                  )}
                </button>
              </div>
            </div>

            {pendingCount === 0 && (
              <div className="text-center text-green-300 py-4">
                <CheckCircle className="w-8 h-8 mx-auto mb-2" />
                <p>   !     .</p>
              </div>
            )}
          </div>

          {/* Analysis Info */}
          <div className="bg-white/10 rounded-xl p-4">
            <h4 className="font-medium text-white mb-3 flex items-center gap-2">
              <FileText className="w-4 h-4 text-amber-400" />
                ?
            </h4>
            <ul className="text-sm text-white/70 space-y-2">
              <li> <strong>ArgumentOutcome</strong> -   </li>
              <li> <strong> </strong> -   </li>
              <li> <strong> </strong> -  ,  </li>
              <li> <strong> </strong> -   </li>
            </ul>
            <p className="text-xs text-white/50 mt-3">
                    (~1-3   ).       .
            </p>
          </div>
        </>
      )}

      {/* Job Status - Visible for both tabs */}
      {status.status !== 'idle' && (
        <div className={`rounded-xl p-4 ${
          status.status === 'running' ? 'bg-blue-500/20 border border-blue-500/50' :
          status.status === 'completed' ? 'bg-green-500/20 border border-green-500/50' :
          status.status === 'failed' ? 'bg-red-500/20 border border-red-500/50' :
          'bg-white/10'
        }`}>
          <div className="flex items-center justify-between mb-3">
            <h3 className="font-semibold text-white flex items-center gap-2">
              {status.status === 'running' && <Loader2 className="w-4 h-4 animate-spin text-blue-400" />}
              {status.status === 'completed' && <CheckCircle className="w-4 h-4 text-green-400" />}
              {status.status === 'failed' && <XCircle className="w-4 h-4 text-red-400" />}
               : {status.job_id}
            </h3>

            {status.status !== 'running' && (
              <button
                onClick={resetJob}
                className="text-sm text-white/60 hover:text-white"
              >
                
              </button>
            )}
          </div>

          {/* Progress Bar */}
          {status.status === 'running' && status.progress && (
            <div className="mb-3">
              <div className="flex justify-between text-sm text-white/70 mb-1">
                <span>
                  
                  {status.progress.stage ? `  ${status.progress.stage}` : ''}
                  {status.progress.type ? ` (${status.progress.type})` : ''}
                  {status.progress.current_file ? `  ${status.progress.current_file}` : ''}
                </span>
                <span>{status.progress.current?.toLocaleString()} / {status.progress.total?.toLocaleString()}</span>
              </div>
              <div className="w-full bg-black/30 rounded-full h-2">
                <div
                  className="bg-blue-500 rounded-full h-2 transition-all"
                  style={{ width: `${((status.progress.current || 0) / (status.progress.total || 1)) * 100}%` }}
                />
              </div>
            </div>
          )}

          {/* Stats */}
          {status.stats && (
            <div className="grid grid-cols-3 gap-4 text-center">
              <div>
                <div className="text-2xl font-bold text-green-400">{status.stats.imported}</div>
                <div className="text-xs text-white/60"></div>
              </div>
              <div>
                <div className="text-2xl font-bold text-yellow-400">{status.stats.duplicates}</div>
                <div className="text-xs text-white/60"></div>
              </div>
              <div>
                <div className="text-2xl font-bold text-red-400">{status.stats.errors}</div>
                <div className="text-xs text-white/60"></div>
              </div>
            </div>
          )}

          {/* Error Message */}
          {status.error && (
            <div className="mt-3 text-sm text-red-300 bg-red-900/30 rounded-lg p-2">
              {status.error}
            </div>
          )}

          {/* Timestamps */}
          <div className="mt-3 flex gap-4 text-xs text-white/50">
            {status.started_at && (
              <span>: {new Date(status.started_at).toLocaleString('he-IL')}</span>
            )}
            {status.completed_at && (
              <span>: {new Date(status.completed_at).toLocaleString('he-IL')}</span>
            )}
          </div>
        </div>
      )}

      {/* Distribution Charts - Import tab only */}
      {activeTab === 'import' && stats && stats.total_rulings > 0 && (
        <div className="grid md:grid-cols-2 gap-6">
          {/* By Court Type */}
          <div className="bg-white/10 rounded-xl p-4">
            <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
              <Building className="w-4 h-4 text-blue-400" />
                 
            </h3>
            <div className="space-y-2">
              {Object.entries(stats.by_court_type || {}).map(([type, count]) => (
                <div key={type} className="flex items-center justify-between">
                  <span className="text-white/80">{COURT_TYPE_LABELS[type] || type}</span>
                  <span className="text-white font-medium">{count.toLocaleString()}</span>
                </div>
              ))}
            </div>
          </div>

          {/* By Year */}
          <div className="bg-white/10 rounded-xl p-4">
            <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
              <Calendar className="w-4 h-4 text-green-400" />
               
            </h3>
            <div className="space-y-2">
              {Object.entries(stats.by_year || {}).slice(0, 10).map(([year, count]) => (
                <div key={year} className="flex items-center justify-between">
                  <span className="text-white/80">{year}</span>
                  <span className="text-white font-medium">{count.toLocaleString()}</span>
                </div>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Top Judges - Import tab only */}
      {activeTab === 'import' && stats && Object.keys(stats.top_judges || {}).length > 0 && (
        <div className="bg-white/10 rounded-xl p-4">
          <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
            <Users className="w-4 h-4 text-purple-400" />
             
          </h3>
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-3">
            {Object.entries(stats.top_judges || {}).slice(0, 12).map(([name, count]) => (
              <div key={name} className="bg-black/20 rounded-lg p-3 flex items-center justify-between">
                <span className="text-white/80 text-sm truncate" title={name}>{name}</span>
                <span className="text-purple-400 font-medium ml-2">{count}</span>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default CourtDecisionsDashboard;
