/**
 * Forensics Dashboard - Admin view of forensic analysis systems
 *
 * Shows:
 * - System statistics and usage metrics
 * - Cross-case pattern analysis
 * - Counsel styleprints (cross-case memory)
 * - Configuration management
 * - Analysis trigger controls
 */

import React, { useState, useEffect, useCallback } from 'react';
import {
  Microscope, Activity, Settings, Users,
  RefreshCw, Search, Trash2,
  BarChart3, TrendingUp, Loader2, Filter, Shield,
  Clock, FileText, Target, Scale, MessageSquare
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPut, apiDelete } from '../../utils/api';
import { logger } from '../../utils/logger';

// Types
interface ForensicsStats {
  total_analyses: number;
  rhetoric_analyses: number;
  contract_analyses: number;
  counsel_analyses: number;
  cross_case_patterns: number;
  avg_analysis_time_ms: number;
  top_detected_patterns: Array<{
    pattern: string;
    category: string;
    count: number;
  }>;
  cases_with_forensics: number;
}

interface PatternFrequency {
  pattern_name: string;
  category: string;
  count: number;
  cases: string[];
  severity_avg: number;
}

interface CounselStyleprint {
  counsel_id: string;
  counsel_name: string | null;
  cases_analyzed: number;
  primary_tone: string;
  aggression_score: number;
  formality_score: number;
  first_seen: string;
  last_seen: string;
}

interface ForensicsConfig {
  rhetoric_sensitivity: number;
  contract_sensitivity: number;
  counsel_sensitivity: number;
  enable_cross_case_memory: boolean;
  auto_analyze_on_upload: boolean;
  max_concurrent_analyses: number;
}

export default function ForensicsDashboard() {
  // State
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState<ForensicsStats | null>(null);
  const [patterns, setPatterns] = useState<PatternFrequency[]>([]);
  const [styleprints, setStyleprints] = useState<CounselStyleprint[]>([]);
  const [config, setConfig] = useState<ForensicsConfig | null>(null);
  const [activeSection, setActiveSection] = useState<'overview' | 'patterns' | 'counsel' | 'config'>('overview');

  // Filter state
  const [patternCategory, setPatternCategory] = useState<string>('');
  const [searchTerm, setSearchTerm] = useState('');
  const [daysFilter] = useState(30);

  // Loading states
  const [savingConfig, setSavingConfig] = useState(false);
  const [deletingStyleprint, setDeletingStyleprint] = useState<string | null>(null);

  // Load data
  const loadStats = useCallback(async () => {
    try {
      const data = await apiGet(`/api/admin/forensics/stats?days=${daysFilter}`, { showErrorToast: false });
      setStats(data);
    } catch (error) {
      logger.error('Failed to load forensics stats:', error);
    }
  }, [daysFilter]);

  const loadPatterns = useCallback(async () => {
    try {
      const params = new URLSearchParams();
      if (patternCategory) params.set('category', patternCategory);
      params.set('min_count', '1');
      params.set('limit', '100');

      const data = await apiGet(`/api/admin/forensics/patterns?${params.toString()}`, { showErrorToast: false });
      setPatterns(data.patterns || []);
    } catch (error) {
      logger.error('Failed to load pattern frequencies:', error);
    }
  }, [patternCategory]);

  const loadStyleprints = useCallback(async () => {
    try {
      const data = await apiGet('/api/admin/forensics/counsel-styleprints?limit=100', { showErrorToast: false });
      setStyleprints(data.styleprints || []);
    } catch (error) {
      logger.error('Failed to load counsel styleprints:', error);
    }
  }, []);

  const loadConfig = useCallback(async () => {
    try {
      const data = await apiGet('/api/admin/forensics/config', { showErrorToast: false });
      setConfig(data);
    } catch (error) {
      logger.error('Failed to load forensics config:', error);
    }
  }, []);

  const loadAll = useCallback(async () => {
    setLoading(true);
    await Promise.all([loadStats(), loadPatterns(), loadStyleprints(), loadConfig()]);
    setLoading(false);
  }, [loadStats, loadPatterns, loadStyleprints, loadConfig]);

  useEffect(() => {
    loadAll();
  }, [loadAll]);

  // Actions
  const handleSaveConfig = async () => {
    if (!config) return;

    setSavingConfig(true);
    try {
      await apiPut('/api/admin/forensics/config', config);
      toast.success('  ');
    } catch (error) {
      logger.error('Failed to save config:', error);
      toast.error('  ');
    } finally {
      setSavingConfig(false);
    }
  };

  const handleDeleteStyleprint = async (counselId: string) => {
    if (!window.confirm('    ?')) return;

    setDeletingStyleprint(counselId);
    try {
      await apiDelete(`/api/admin/forensics/counsel-styleprints/${counselId}`);
      toast.success('  ');
      loadStyleprints();
    } catch (error) {
      logger.error('Failed to delete styleprint:', error);
      toast.error(' ');
    } finally {
      setDeletingStyleprint(null);
    }
  };

  // Filtered styleprints
  const filteredStyleprints = styleprints.filter(sp =>
    sp.counsel_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    sp.counsel_id.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Category color helper
  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'rhetoric': return 'bg-blue-500/20 text-blue-300';
      case 'contract': return 'bg-purple-500/20 text-purple-300';
      case 'counsel': return 'bg-orange-500/20 text-orange-300';
      default: return 'bg-gray-500/20 text-gray-300';
    }
  };

  // Tone color helper
  const getToneColor = (tone: string) => {
    switch (tone) {
      case 'aggressive': return 'text-red-400';
      case 'assertive': return 'text-orange-400';
      case 'neutral': return 'text-gray-400';
      case 'conciliatory': return 'text-green-400';
      case 'defensive': return 'text-blue-400';
      default: return 'text-gray-400';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="w-8 h-8 animate-spin text-purple-400" />
        <span className="mr-3 text-white/70">  ...</span>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <Microscope className="w-8 h-8 text-purple-400" />
          <h2 className="text-2xl font-bold text-white"> </h2>
        </div>
        <button
          onClick={loadAll}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 rounded-lg transition"
        >
          <RefreshCw size={18} className="text-white" />
          <span className="text-white"></span>
        </button>
      </div>

      {/* Section Tabs */}
      <div className="flex gap-2 border-b border-white/20 pb-2">
        {[
          { id: 'overview', label: ' ', icon: BarChart3 },
          { id: 'patterns', label: '', icon: Target },
          { id: 'counsel', label: ' ', icon: Users },
          { id: 'config', label: '', icon: Settings },
        ].map(({ id, label, icon: Icon }) => (
          <button
            key={id}
            onClick={() => setActiveSection(id as any)}
            className={`flex items-center gap-2 px-4 py-2 rounded-t-lg font-medium transition ${
              activeSection === id
                ? 'bg-purple-600 text-white'
                : 'bg-white/5 text-purple-300 hover:bg-white/10'
            }`}
          >
            <Icon size={18} />
            {label}
          </button>
        ))}
      </div>

      {/* Overview Section */}
      {activeSection === 'overview' && stats && (
        <div className="space-y-6">
          {/* Stats Cards */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-3">
                <Activity className="h-8 w-8 text-blue-400" />
                <div>
                  <p className="text-white/70 text-sm">" </p>
                  <p className="text-3xl font-bold text-white">{stats.total_analyses}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-3">
                <FileText className="h-8 w-8 text-purple-400" />
                <div>
                  <p className="text-white/70 text-sm">  </p>
                  <p className="text-3xl font-bold text-white">{stats.cases_with_forensics}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-3">
                <Target className="h-8 w-8 text-orange-400" />
                <div>
                  <p className="text-white/70 text-sm"> -</p>
                  <p className="text-3xl font-bold text-white">{stats.cross_case_patterns}</p>
                </div>
              </div>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-3">
                <Clock className="h-8 w-8 text-green-400" />
                <div>
                  <p className="text-white/70 text-sm">  </p>
                  <p className="text-3xl font-bold text-white">{stats.avg_analysis_time_ms.toFixed(0)}ms</p>
                </div>
              </div>
            </div>
          </div>

          {/* Analysis Breakdown */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-2 mb-4">
                <MessageSquare className="text-blue-400" size={20} />
                <h3 className="text-lg font-semibold text-white"> </h3>
              </div>
              <p className="text-4xl font-bold text-blue-400">{stats.rhetoric_analyses}</p>
              <p className="text-white/50 text-sm mt-2">  ,  </p>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-2 mb-4">
                <Scale className="text-purple-400" size={20} />
                <h3 className="text-lg font-semibold text-white"> </h3>
              </div>
              <p className="text-4xl font-bold text-purple-400">{stats.contract_analyses}</p>
              <p className="text-white/50 text-sm mt-2"> , </p>
            </div>

            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <div className="flex items-center gap-2 mb-4">
                <Users className="text-orange-400" size={20} />
                <h3 className="text-lg font-semibold text-white"> </h3>
              </div>
              <p className="text-4xl font-bold text-orange-400">{stats.counsel_analyses}</p>
              <p className="text-white/50 text-sm mt-2">, ,  </p>
            </div>
          </div>

          {/* Top Patterns */}
          {stats.top_detected_patterns.length > 0 && (
            <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
              <h3 className="text-lg font-semibold text-white mb-4 flex items-center gap-2">
                <TrendingUp className="text-green-400" size={20} />
                 
              </h3>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {stats.top_detected_patterns.map((pattern, idx) => (
                  <div key={idx} className="bg-white/5 rounded-lg p-4">
                    <div className="flex items-center justify-between mb-2">
                      <span className="text-white font-medium">{pattern.pattern}</span>
                      <span className={`px-2 py-1 rounded text-xs ${getCategoryColor(pattern.category)}`}>
                        {pattern.category}
                      </span>
                    </div>
                    <p className="text-2xl font-bold text-white">{pattern.count}</p>
                    <p className="text-white/50 text-sm"></p>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      )}

      {/* Patterns Section */}
      {activeSection === 'patterns' && (
        <div className="space-y-4">
          {/* Filters */}
          <div className="flex gap-4 items-center">
            <select
              value={patternCategory}
              onChange={(e) => setPatternCategory(e.target.value)}
              className="bg-white/10 border border-white/20 rounded-lg px-4 py-2 text-white"
            >
              <option value=""> </option>
              <option value="rhetoric"></option>
              <option value="contract"></option>
              <option value="counsel"></option>
            </select>
            <button
              onClick={loadPatterns}
              className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg transition text-white"
            >
              <Filter size={16} />
              
            </button>
          </div>

          {/* Patterns Table */}
          <div className="bg-white/10 backdrop-blur-lg rounded-lg border border-white/20 overflow-hidden">
            <table className="w-full">
              <thead className="bg-white/5">
                <tr>
                  <th className="px-6 py-3 text-right text-sm font-semibold text-white/70"></th>
                  <th className="px-6 py-3 text-right text-sm font-semibold text-white/70"></th>
                  <th className="px-6 py-3 text-right text-sm font-semibold text-white/70"></th>
                  <th className="px-6 py-3 text-right text-sm font-semibold text-white/70"></th>
                  <th className="px-6 py-3 text-right text-sm font-semibold text-white/70"> </th>
                </tr>
              </thead>
              <tbody>
                {patterns.map((pattern, idx) => (
                  <tr key={idx} className="border-t border-white/10 hover:bg-white/5">
                    <td className="px-6 py-4 text-white font-medium">{pattern.pattern_name}</td>
                    <td className="px-6 py-4">
                      <span className={`px-2 py-1 rounded text-xs ${getCategoryColor(pattern.category)}`}>
                        {pattern.category}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-white">{pattern.count}</td>
                    <td className="px-6 py-4 text-white/70">{pattern.cases.length}</td>
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-2">
                        <div className="flex-1 h-2 bg-white/10 rounded-full overflow-hidden max-w-[100px]">
                          <div
                            className={`h-full ${
                              pattern.severity_avg > 0.7 ? 'bg-red-500' :
                              pattern.severity_avg > 0.4 ? 'bg-orange-500' : 'bg-green-500'
                            }`}
                            style={{ width: `${pattern.severity_avg * 100}%` }}
                          />
                        </div>
                        <span className="text-white/70 text-sm">{(pattern.severity_avg * 100).toFixed(0)}%</span>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {patterns.length === 0 && (
              <div className="text-center py-12 text-white/50">
                  
              </div>
            )}
          </div>
        </div>
      )}

      {/* Counsel Styleprints Section */}
      {activeSection === 'counsel' && (
        <div className="space-y-4">
          {/* Search */}
          <div className="relative max-w-md">
            <Search className="absolute right-4 top-1/2 -translate-y-1/2 text-gray-400" size={20} />
            <input
              type="text"
              placeholder="    ..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pr-12 pl-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400"
            />
          </div>

          {/* Styleprints Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {filteredStyleprints.map((sp) => (
              <div key={sp.counsel_id} className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <h3 className="text-lg font-semibold text-white">
                      {sp.counsel_name || ' '}
                    </h3>
                    <p className="text-white/50 text-sm">{sp.counsel_id}</p>
                  </div>
                  <button
                    onClick={() => handleDeleteStyleprint(sp.counsel_id)}
                    disabled={deletingStyleprint === sp.counsel_id}
                    className="p-2 text-red-400 hover:bg-red-500/20 rounded transition"
                  >
                    {deletingStyleprint === sp.counsel_id ? (
                      <Loader2 size={16} className="animate-spin" />
                    ) : (
                      <Trash2 size={16} />
                    )}
                  </button>
                </div>

                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <span className="text-white/70"> </span>
                    <span className={`font-medium ${getToneColor(sp.primary_tone)}`}>
                      {sp.primary_tone}
                    </span>
                  </div>

                  <div className="flex items-center justify-between">
                    <span className="text-white/70"> </span>
                    <span className="text-white font-medium">{sp.cases_analyzed}</span>
                  </div>

                  <div>
                    <div className="flex items-center justify-between mb-1">
                      <span className="text-white/70 text-sm"></span>
                      <span className="text-white/70 text-sm">{sp.aggression_score.toFixed(0)}%</span>
                    </div>
                    <div className="h-2 bg-white/10 rounded-full overflow-hidden">
                      <div
                        className="h-full bg-red-500"
                        style={{ width: `${sp.aggression_score}%` }}
                      />
                    </div>
                  </div>

                  <div>
                    <div className="flex items-center justify-between mb-1">
                      <span className="text-white/70 text-sm"></span>
                      <span className="text-white/70 text-sm">{sp.formality_score.toFixed(0)}%</span>
                    </div>
                    <div className="h-2 bg-white/10 rounded-full overflow-hidden">
                      <div
                        className="h-full bg-blue-500"
                        style={{ width: `${sp.formality_score}%` }}
                      />
                    </div>
                  </div>

                  <div className="pt-2 border-t border-white/10 text-xs text-white/50">
                     : {new Date(sp.last_seen).toLocaleDateString('he-IL')}
                  </div>
                </div>
              </div>
            ))}
          </div>

          {filteredStyleprints.length === 0 && (
            <div className="text-center py-12 bg-white/10 backdrop-blur-lg rounded-lg border border-white/20">
              <Users className="mx-auto text-white/30 mb-4" size={48} />
              <p className="text-white/50">   </p>
            </div>
          )}
        </div>
      )}

      {/* Configuration Section */}
      {activeSection === 'config' && config && (
        <div className="bg-white/10 backdrop-blur-lg rounded-lg p-6 border border-white/20">
          <h3 className="text-lg font-semibold text-white mb-6 flex items-center gap-2">
            <Settings className="text-gray-400" size={20} />
              
          </h3>

          <div className="space-y-6">
            {/* Sensitivity Sliders */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div>
                <label className="block text-white/70 mb-2"> </label>
                <input
                  type="range"
                  min="0"
                  max="1"
                  step="0.1"
                  value={config.rhetoric_sensitivity}
                  onChange={(e) => setConfig({ ...config, rhetoric_sensitivity: parseFloat(e.target.value) })}
                  className="w-full"
                />
                <div className="text-center text-white mt-1">
                  {(config.rhetoric_sensitivity * 100).toFixed(0)}%
                </div>
              </div>

              <div>
                <label className="block text-white/70 mb-2"> </label>
                <input
                  type="range"
                  min="0"
                  max="1"
                  step="0.1"
                  value={config.contract_sensitivity}
                  onChange={(e) => setConfig({ ...config, contract_sensitivity: parseFloat(e.target.value) })}
                  className="w-full"
                />
                <div className="text-center text-white mt-1">
                  {(config.contract_sensitivity * 100).toFixed(0)}%
                </div>
              </div>

              <div>
                <label className="block text-white/70 mb-2"> </label>
                <input
                  type="range"
                  min="0"
                  max="1"
                  step="0.1"
                  value={config.counsel_sensitivity}
                  onChange={(e) => setConfig({ ...config, counsel_sensitivity: parseFloat(e.target.value) })}
                  className="w-full"
                />
                <div className="text-center text-white mt-1">
                  {(config.counsel_sensitivity * 100).toFixed(0)}%
                </div>
              </div>
            </div>

            {/* Toggle Options */}
            <div className="space-y-4">
              <label className="flex items-center gap-3 cursor-pointer">
                <input
                  type="checkbox"
                  checked={config.enable_cross_case_memory}
                  onChange={(e) => setConfig({ ...config, enable_cross_case_memory: e.target.checked })}
                  className="w-5 h-5 rounded bg-white/10 border-white/30"
                />
                <span className="text-white">  -</span>
              </label>

              <label className="flex items-center gap-3 cursor-pointer">
                <input
                  type="checkbox"
                  checked={config.auto_analyze_on_upload}
                  onChange={(e) => setConfig({ ...config, auto_analyze_on_upload: e.target.checked })}
                  className="w-5 h-5 rounded bg-white/10 border-white/30"
                />
                <span className="text-white">  </span>
              </label>
            </div>

            {/* Max Concurrent */}
            <div>
              <label className="block text-white/70 mb-2">  </label>
              <input
                type="number"
                min="1"
                max="20"
                value={config.max_concurrent_analyses}
                onChange={(e) => setConfig({ ...config, max_concurrent_analyses: parseInt(e.target.value) || 5 })}
                className="w-32 bg-white/10 border border-white/20 rounded-lg px-4 py-2 text-white"
              />
            </div>

            {/* Save Button */}
            <div className="pt-4 border-t border-white/20">
              <button
                onClick={handleSaveConfig}
                disabled={savingConfig}
                className="flex items-center gap-2 px-6 py-3 bg-green-600 hover:bg-green-700 disabled:bg-gray-600 rounded-lg transition text-white"
              >
                {savingConfig ? (
                  <>
                    <Loader2 size={18} className="animate-spin" />
                    ...
                  </>
                ) : (
                  <>
                    <Shield size={18} />
                     
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
