/**
 * Legal Statistics Dashboard -   
 *
 * Displays aggregate legal statistics from analyzed court rulings.
 * Privacy-compliant: Shows only statistical data, no personality assessments.
 *
 * Features:
 * - Argument success rates by type
 * - Court tendencies comparison
 * - Case type statistics
 * - Judge/Lawyer search with stats
 */

import React, { useState, useEffect, useCallback } from 'react';
import {
  BarChart3, Search, TrendingUp, TrendingDown, Scale, Building,
  Users, Gavel, FileText, RefreshCw, Loader2, AlertCircle,
  ChevronDown, ChevronUp, Filter, Info
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPost } from '../../utils/api';
import { logger } from '../../utils/logger';

// Types
interface ArgumentStats {
  success: boolean;
  argument_type: string;
  case_type?: string;
  court_type?: string;
  total_cases: number;
  rates: Record<string, { count: number; rate: number }>;
  insights: string[];
  disclaimer: string;
}

interface CourtTendencies {
  success: boolean;
  court_type: string;
  tendencies: Record<string, {
    total: number;
    success_rate: number;
    acceptance_rate: number;
    rejection_rate: number;
  }>;
  summary: {
    strongest_arguments: Array<{ type: string; rate: number }>;
    weakest_arguments: Array<{ type: string; rate: number }>;
  };
}

interface CaseTypeStats {
  success: boolean;
  case_types: Record<string, {
    total_arguments: number;
    overall_acceptance_rate: number;
    overall_rejection_rate: number;
  }>;
}

interface JudgeSearchResult {
  id: string;
  name: string;
  court: string;
  court_type: string;
  rulings_analyzed: number;
  plaintiff_win_rate: number;
  motion_statistics?: Record<string, any>;
  argument_statistics?: Record<string, any>;
}

interface LawyerSearchResult {
  id: string;
  name: string;
  firm_name?: string;
  cases_analyzed: number;
  win_rate: number;
  argument_statistics?: Record<string, any>;
}

// Argument types for dropdown
const ARGUMENT_TYPES = [
  '',
  '',
  ' ',
  '',
  '',
  '',
  '',
  '',
  '',
  ' ',
];

const CASE_TYPES = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
];

const COURT_TYPES = [
  { value: '', label: '  ' },
  { value: '', label: '  ' },
  { value: '', label: '  ' },
  { value: '', label: '  ' },
  { value: '', label: '   ' },
  { value: '_', label: ' ' },
];

//   
const COURT_LOCATIONS = [
  { value: '_', label: ' ' },
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '_', label: ' ' },
  { value: '', label: '' },
  { value: '', label: ' ()' },
  { value: '_', label: ' ' },
  { value: '_', label: ' ' },
  { value: '', label: '' },
  { value: '_', label: ' ' },
  { value: '', label: '' },
  { value: '_', label: ' ' },
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '', label: '' },
  { value: '', label: '' },
];

const LegalStatisticsDashboard: React.FC = () => {
  // State
  const [loading, setLoading] = useState(false);
  const [activeTab, setActiveTab] = useState<'arguments' | 'courts' | 'cases'>('arguments');

  // Argument stats
  const [selectedArgumentType, setSelectedArgumentType] = useState(ARGUMENT_TYPES[0]);
  const [selectedCaseType, setSelectedCaseType] = useState<string>('');
  const [selectedCourtType, setSelectedCourtType] = useState<string>('');
  const [selectedCourtLocation, setSelectedCourtLocation] = useState<string>('');
  const [selectedJudgeName, setSelectedJudgeName] = useState<string>(''); //   
  const [argumentStats, setArgumentStats] = useState<ArgumentStats | null>(null);

  // Court tendencies
  const [courtTendencies, setCourtTendencies] = useState<CourtTendencies | null>(null);
  const [selectedCourtForTendencies, setSelectedCourtForTendencies] = useState(COURT_TYPES[1].value);
  const [selectedLocationForTendencies, setSelectedLocationForTendencies] = useState<string>('');

  // Case type stats
  const [caseTypeStats, setCaseTypeStats] = useState<CaseTypeStats | null>(null);

  // Note: Search for individual judges/lawyers removed - focusing on aggregate case statistics only

  // Fetch argument success rates
  //    :  ,  , , ,  ()
  const fetchArgumentStats = useCallback(async () => {
    setLoading(true);
    try {
      const params = new URLSearchParams({
        argument_type: selectedArgumentType,
      });
      if (selectedCaseType) params.append('case_type', selectedCaseType);
      if (selectedCourtType) params.append('court_type', selectedCourtType);
      if (selectedCourtLocation) params.append('court_location', selectedCourtLocation);
      if (selectedJudgeName) params.append('judge_name', selectedJudgeName);

      const data = await apiGet<ArgumentStats>(`/api/statistics/argument-success?${params}`);
      setArgumentStats(data);
    } catch (error: any) {
      if (error.status === 404) {
        setArgumentStats(null);
        toast.info('    ');
      } else {
        logger.error('Error fetching argument stats:', error);
        toast.error('  ');
      }
    } finally {
      setLoading(false);
    }
  }, [selectedArgumentType, selectedCaseType, selectedCourtType, selectedCourtLocation, selectedJudgeName]);

  // Fetch court tendencies
  const fetchCourtTendencies = useCallback(async () => {
    setLoading(true);
    try {
      const params = new URLSearchParams({
        court_type: selectedCourtForTendencies,
      });

      const data = await apiGet<CourtTendencies>(`/api/statistics/court-tendencies?${params}`);
      setCourtTendencies(data);
    } catch (error: any) {
      logger.error('Error fetching court tendencies:', error);
      setCourtTendencies(null);
    } finally {
      setLoading(false);
    }
  }, [selectedCourtForTendencies]);

  // Fetch case type stats
  const fetchCaseTypeStats = useCallback(async () => {
    setLoading(true);
    try {
      const data = await apiGet<CaseTypeStats>('/api/statistics/case-types');
      setCaseTypeStats(data);
    } catch (error: any) {
      logger.error('Error fetching case type stats:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  // Note: handleSearch function removed - focusing on aggregate statistics only,
  // while still allowing mention of judge/lawyer names in case context

  // Effect for tab changes
  useEffect(() => {
    if (activeTab === 'arguments') {
      fetchArgumentStats();
    } else if (activeTab === 'courts') {
      fetchCourtTendencies();
    } else if (activeTab === 'cases') {
      fetchCaseTypeStats();
    }
  }, [activeTab, fetchArgumentStats, fetchCourtTendencies, fetchCaseTypeStats]);

  // Render success rate bar
  const renderRateBar = (rate: number, color: string = 'blue') => {
    const colorClasses = {
      blue: 'bg-blue-500',
      green: 'bg-green-500',
      red: 'bg-red-500',
      yellow: 'bg-yellow-500',
    };

    return (
      <div className="flex items-center gap-2">
        <div className="flex-1 h-2 bg-white/10 rounded-full overflow-hidden">
          <div
            className={`h-full ${colorClasses[color as keyof typeof colorClasses] || 'bg-blue-500'} rounded-full transition-all duration-500`}
            style={{ width: `${rate}%` }}
          />
        </div>
        <span className="text-sm font-medium text-white min-w-[45px] text-left">
          {rate.toFixed(1)}%
        </span>
      </div>
    );
  };

  return (
    <div className="space-y-6" dir="rtl">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="w-12 h-12 bg-blue-500/20 rounded-xl flex items-center justify-center">
            <BarChart3 className="w-6 h-6 text-blue-400" />
          </div>
          <div>
            <h2 className="text-xl font-bold text-white"> </h2>
            <p className="text-sm text-white/60">    </p>
          </div>
        </div>

        {/* Privacy badge */}
        <div className="flex items-center gap-2 px-3 py-1.5 bg-green-500/20 border border-green-500/30 rounded-lg">
          <Info className="w-4 h-4 text-green-400" />
          <span className="text-sm text-green-300">  </span>
        </div>
      </div>

      {/* Tabs - Focus on case/claim statistics, not personal profiles */}
      <div className="flex gap-2 border-b border-white/10 pb-2">
        {[
          { id: 'arguments', label: ' ', icon: FileText },
          { id: 'courts', label: '  ', icon: Building },
          { id: 'cases', label: ' ', icon: Scale },
          // Note: Search tab for judge/lawyer profiles removed - focusing on case statistics only
        ].map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id as any)}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
              activeTab === tab.id
                ? 'bg-blue-500/20 text-blue-400'
                : 'text-white/60 hover:text-white hover:bg-white/5'
            }`}
          >
            <tab.icon className="w-4 h-4" />
            {tab.label}
          </button>
        ))}
      </div>

      {/* Argument Success Tab */}
      {activeTab === 'arguments' && (
        <div className="space-y-6">
          {/* Filters */}
          <div className="bg-white/5 rounded-xl p-4">
            <div className="grid md:grid-cols-4 gap-4">
              <div>
                <label className="block text-sm text-white/70 mb-1"> </label>
                <select
                  value={selectedArgumentType}
                  onChange={(e) => setSelectedArgumentType(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                >
                  {ARGUMENT_TYPES.map(type => (
                    <option key={type} value={type}>{type}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm text-white/70 mb-1">  ()</label>
                <select
                  value={selectedCaseType}
                  onChange={(e) => setSelectedCaseType(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                >
                  <option value=""></option>
                  {CASE_TYPES.map(type => (
                    <option key={type} value={type}>{type}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm text-white/70 mb-1">   ()</label>
                <select
                  value={selectedCourtType}
                  onChange={(e) => setSelectedCourtType(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                >
                  <option value=""></option>
                  {COURT_TYPES.map(type => (
                    <option key={type.value} value={type.value}>{type.label}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm text-white/70 mb-1">   ()</label>
                <select
                  value={selectedCourtLocation}
                  onChange={(e) => setSelectedCourtLocation(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                >
                  <option value=""></option>
                  {COURT_LOCATIONS.map(loc => (
                    <option key={loc.value} value={loc.value}>{loc.label}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm text-white/70 mb-1">  ()</label>
                <input
                  type="text"
                  value={selectedJudgeName}
                  onChange={(e) => setSelectedJudgeName(e.target.value)}
                  placeholder=": "
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white placeholder-white/40"
                />
                <p className="text-xs text-white/50 mt-1">   </p>
              </div>

              <div className="flex items-end">
                <button
                  onClick={fetchArgumentStats}
                  disabled={loading}
                  className="w-full px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600 text-white rounded-lg font-medium flex items-center justify-center gap-2"
                >
                  {loading ? (
                    <Loader2 className="w-4 h-4 animate-spin" />
                  ) : (
                    <RefreshCw className="w-4 h-4" />
                  )}
                  
                </button>
              </div>
            </div>
          </div>

          {/* Results */}
          {argumentStats ? (
            <div className="bg-white/10 rounded-xl p-6">
              <div className="flex items-center justify-between mb-6">
                <h3 className="text-lg font-semibold text-white">
                   {argumentStats.argument_type}
                  {argumentStats.case_type && `  ${argumentStats.case_type}`}
                </h3>
                <div className="text-sm text-white/60">
                    {argumentStats.total_cases} 
                </div>
              </div>

              {/* Rates */}
              <div className="space-y-4 mb-6">
                {Object.entries(argumentStats.rates).map(([outcome, data]) => (
                  <div key={outcome}>
                    <div className="flex justify-between text-sm text-white/80 mb-1">
                      <span>
                        {outcome === 'accepted' && ''}
                        {outcome === 'rejected' && ''}
                        {outcome === 'partially_accepted' && ' '}
                        {outcome === 'not_addressed' && ' '}
                      </span>
                      <span>{data.count} </span>
                    </div>
                    {renderRateBar(
                      data.rate,
                      outcome === 'accepted' ? 'green' :
                      outcome === 'rejected' ? 'red' :
                      outcome === 'partially_accepted' ? 'yellow' : 'blue'
                    )}
                  </div>
                ))}
              </div>

              {/* Insights */}
              {argumentStats.insights?.length > 0 && (
                <div className="bg-blue-500/10 border border-blue-500/30 rounded-lg p-4">
                  <h4 className="font-medium text-blue-300 mb-2"></h4>
                  <ul className="space-y-1 text-sm text-blue-200/80">
                    {argumentStats.insights.map((insight, i) => (
                      <li key={i}> {insight}</li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          ) : !loading && (
            <div className="bg-white/5 rounded-xl p-12 text-center">
              <AlertCircle className="w-12 h-12 text-white/30 mx-auto mb-4" />
              <p className="text-white/60">    </p>
              <p className="text-sm text-white/40 mt-2">  10  </p>
            </div>
          )}
        </div>
      )}

      {/* Court Tendencies Tab */}
      {activeTab === 'courts' && (
        <div className="space-y-6">
          {/* Court selector */}
          <div className="bg-white/5 rounded-xl p-4">
            <div className="flex gap-4 items-end">
              <div className="flex-1">
                <label className="block text-sm text-white/70 mb-1"> </label>
                <select
                  value={selectedCourtForTendencies}
                  onChange={(e) => setSelectedCourtForTendencies(e.target.value)}
                  className="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white"
                >
                  {COURT_TYPES.map(type => (
                    <option key={type.value} value={type.value}>{type.label}</option>
                  ))}
                </select>
              </div>
              <button
                onClick={fetchCourtTendencies}
                disabled={loading}
                className="px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-gray-600 text-white rounded-lg font-medium flex items-center gap-2"
              >
                {loading ? <Loader2 className="w-4 h-4 animate-spin" /> : <RefreshCw className="w-4 h-4" />}
                
              </button>
            </div>
          </div>

          {/* Tendencies results */}
          {courtTendencies?.summary && (
            <div className="grid md:grid-cols-2 gap-6">
              {/* Strongest arguments */}
              <div className="bg-green-500/10 border border-green-500/30 rounded-xl p-4">
                <h3 className="font-semibold text-green-300 mb-4 flex items-center gap-2">
                  <TrendingUp className="w-5 h-5" />
                   
                </h3>
                <div className="space-y-3">
                  {courtTendencies.summary.strongest_arguments?.map((arg, i) => (
                    <div key={i} className="flex justify-between items-center">
                      <span className="text-white/80">{arg.type}</span>
                      <span className="text-green-400 font-medium">{arg.rate.toFixed(1)}%</span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Weakest arguments */}
              <div className="bg-red-500/10 border border-red-500/30 rounded-xl p-4">
                <h3 className="font-semibold text-red-300 mb-4 flex items-center gap-2">
                  <TrendingDown className="w-5 h-5" />
                   
                </h3>
                <div className="space-y-3">
                  {courtTendencies.summary.weakest_arguments?.map((arg, i) => (
                    <div key={i} className="flex justify-between items-center">
                      <span className="text-white/80">{arg.type}</span>
                      <span className="text-red-400 font-medium">{arg.rate.toFixed(1)}%</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}

          {/* Full tendencies table */}
          {courtTendencies?.tendencies && Object.keys(courtTendencies.tendencies).length > 0 && (
            <div className="bg-white/10 rounded-xl overflow-hidden">
              <table className="w-full">
                <thead className="bg-white/5">
                  <tr>
                    <th className="text-right px-4 py-3 text-sm font-medium text-white/70"> </th>
                    <th className="text-center px-4 py-3 text-sm font-medium text-white/70">"</th>
                    <th className="text-center px-4 py-3 text-sm font-medium text-white/70"></th>
                    <th className="text-center px-4 py-3 text-sm font-medium text-white/70"></th>
                    <th className="text-center px-4 py-3 text-sm font-medium text-white/70"></th>
                  </tr>
                </thead>
                <tbody>
                  {Object.entries(courtTendencies.tendencies).map(([type, data]) => (
                    <tr key={type} className="border-t border-white/5">
                      <td className="px-4 py-3 text-white/80">{type}</td>
                      <td className="px-4 py-3 text-center text-white/60">{data.total}</td>
                      <td className="px-4 py-3 text-center">
                        <span className={`font-medium ${data.success_rate >= 50 ? 'text-green-400' : 'text-red-400'}`}>
                          {data.success_rate.toFixed(1)}%
                        </span>
                      </td>
                      <td className="px-4 py-3 text-center text-green-400">{data.acceptance_rate.toFixed(1)}%</td>
                      <td className="px-4 py-3 text-center text-red-400">{data.rejection_rate.toFixed(1)}%</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      )}

      {/* Case Types Tab */}
      {activeTab === 'cases' && (
        <div className="space-y-6">
          {caseTypeStats?.case_types && Object.keys(caseTypeStats.case_types).length > 0 ? (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
              {Object.entries(caseTypeStats.case_types).map(([caseType, stats]) => (
                <div key={caseType} className="bg-white/10 rounded-xl p-4">
                  <h3 className="font-semibold text-white mb-3 flex items-center gap-2">
                    <Scale className="w-4 h-4 text-purple-400" />
                    {caseType}
                  </h3>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-white/60"></span>
                      <span className="text-white">{stats.total_arguments}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-white/60"> </span>
                      <span className="text-green-400">{stats.overall_acceptance_rate.toFixed(1)}%</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-white/60"> </span>
                      <span className="text-red-400">{stats.overall_rejection_rate.toFixed(1)}%</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : !loading && (
            <div className="bg-white/5 rounded-xl p-12 text-center">
              <AlertCircle className="w-12 h-12 text-white/30 mx-auto mb-4" />
              <p className="text-white/60">  </p>
            </div>
          )}
        </div>
      )}

      {/* Note: Search Tab for individual judge/lawyer profiles removed.
          Statistics focus on aggregate case/claim data without personal identification. */}

      {/* Loading overlay */}
      {loading && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-gray-800 rounded-xl p-6 flex items-center gap-3">
            <Loader2 className="w-6 h-6 text-blue-400 animate-spin" />
            <span className="text-white"> ...</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default LegalStatisticsDashboard;
