/**
 * Memory & Learning Dashboard - Admin view of all memory systems
 *
 * Shows:
 * - Memory overview statistics
 * - User memory profiles list
 * - Learning level distribution
 * - Case memory analytics
 * - Agent performance
 */

import React, { useState, useEffect, useCallback } from 'react';
import {
  Brain, Users, Database, RefreshCw,
  AlertTriangle, CheckCircle,
  BarChart3, Loader2, Eye, Filter,
  Sparkles, MessageSquare, FileText, Target, Shield,
  Zap, UserCircle, Award, Activity
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPost } from '../../utils/api';
import { logger } from '../../utils/logger';

// Types
interface MemoryOverview {
  overview: {
    total_users: number;
    users_with_profile: number;
    users_with_learning: number;
    profile_coverage_percent: number;
  };
  learning_levels: Record<number, number>;
  tier_distribution: Record<string, number>;
  case_memory: {
    total_cases: number;
    cases_with_memory: number;
    memory_coverage_percent: number;
  };
  chat_stats: {
    total_sessions: number;
    total_messages: number;
    avg_messages_per_session: number;
  };
  profile_completeness: {
    complete: number;
    partial: number;
    empty: number;
  };
}

interface UserMemoryItem {
  id: string;
  email: string;
  name: string;
  tier: string;
  learning_level: number;
  memory_score: number;
  has_profile: boolean;
  total_relationships: number;
  case_count: number;
  message_count: number;
  total_tokens: number;
  monthly_tokens: number;
  total_cost_cents: number;
  last_active: string | null;
}

interface UserMemoryDetails {
  user: {
    id: string;
    email: string;
    name: string;
    tier: string;
    role: string;
    created_at: string;
    last_active: string;
  };
  profile: {
    professional_identity: Record<string, any>;
    work_preferences: Record<string, any>;
    professional_relationships: Record<string, any[]>;
    usage_patterns: Record<string, any>;
    feedback_history: Record<string, any[]>;
    learning_enabled: boolean;
    completeness: number;
  } | null;
  learning: {
    level: number;
    confidence_scores: Record<string, number>;
    interaction_patterns: Record<string, any>;
    tool_preferences: Record<string, any>;
  };
  cases: Array<{
    id: string;
    name: string;
    status: string;
    has_memory: boolean;
    memory_keys: string[];
    claims_count: number;
    contradictions_count: number;
    entities_count: number;
    tokens_used: number;
  }>;
  chat_stats: {
    total_sessions: number;
    total_messages: number;
    total_tokens: number;
  };
  token_usage: {
    total: number;
    monthly: number;
  };
  cost: {
    total_cents: number;
    monthly_cents: number;
  };
}

interface LearningAnalytics {
  period_days: number;
  level_distribution: Record<number, number>;
  totals: {
    users_with_learning: number;
    total_interactions: number;
    total_corrections: number;
    total_feedback: number;
  };
  averages: {
    avg_learning_level: number;
    avg_interactions_per_user: number;
  };
  effectiveness: {
    correction_rate: number;
    feedback_rate: number;
  };
}

interface CaseAnalytics {
  totals: {
    cases: number;
    cases_with_memory: number;
    claims: number;
    contradictions: number;
    entities: number;
    tokens_used: number;
  };
  memory_types: Record<string, number>;
  status_distribution: Record<string, number>;
  averages: {
    claims_per_case: number;
    contradictions_per_case: number;
    entities_per_case: number;
    tokens_per_case: number;
  };
  coverage: {
    memory_coverage_percent: number;
  };
}

interface AgentAnalytics {
  period_days: number;
  cases_analyzed: number;
  agent_stats: Record<string, {
    runs: number;
    success: number;
    success_rate: number;
    total_pages?: number;
    total_claims?: number;
    total_entities?: number;
    total_found?: number;
    total_insights?: number;
  }>;
}

// Learning level descriptions
const LEARNING_LEVELS: Record<number, { name: string; description: string; color: string }> = {
  0: { name: ' ', description: 'Free tier', color: 'gray' },
  1: { name: ' ', description: ' ', color: 'blue' },
  2: { name: ' ', description: ' ', color: 'cyan' },
  3: { name: ' -', description: ' ', color: 'green' },
  4: { name: ' -', description: ' ', color: 'yellow' },
  5: { name: '-', description: ' ', color: 'orange' },
  6: { name: ' ', description: 'Q-learning', color: 'red' },
  7: { name: '  ', description: '  ', color: 'purple' },
};

// Tier colors
const TIER_COLORS: Record<string, string> = {
  free: 'text-gray-400',
  basic: 'text-blue-400',
  professional: 'text-purple-400',
  enterprise: 'text-amber-400',
  admin: 'text-red-400',
};

export default function MemoryLearningDashboard() {
  // State
  const [loading, setLoading] = useState(true);
  const [overview, setOverview] = useState<MemoryOverview | null>(null);
  const [users, setUsers] = useState<UserMemoryItem[]>([]);
  const [selectedUser, setSelectedUser] = useState<UserMemoryDetails | null>(null);
  const [learningAnalytics, setLearningAnalytics] = useState<LearningAnalytics | null>(null);
  const [caseAnalytics, setCaseAnalytics] = useState<CaseAnalytics | null>(null);
  const [agentAnalytics, setAgentAnalytics] = useState<AgentAnalytics | null>(null);

  // Filters
  const [filterTier, setFilterTier] = useState<string>('');
  const [sortBy, setSortBy] = useState<string>('last_active');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  // UI State
  const [activeTab, setActiveTab] = useState<'overview' | 'users' | 'learning' | 'cases' | 'agents'>('overview');
  const [showUserDetails, setShowUserDetails] = useState(false);

  // Load overview data
  const loadOverview = useCallback(async () => {
    try {
      const data = await apiGet('/api/admin/memory/overview', { showErrorToast: false });
      setOverview(data);
    } catch (error) {
      logger.error('Failed to load memory overview:', error);
      toast.error('   ');
    }
  }, []);

  // Load users list
  const loadUsers = useCallback(async () => {
    try {
      const params = new URLSearchParams({
        page: page.toString(),
        limit: '20',
        sort_by: sortBy,
      });
      if (filterTier) params.append('filter_tier', filterTier);

      const data = await apiGet(`/api/admin/memory/users?${params}`, { showErrorToast: false });
      setUsers(data.users);
      setTotalPages(data.pagination.pages);
    } catch (error) {
      logger.error('Failed to load users:', error);
      toast.error('  ');
    }
  }, [page, sortBy, filterTier]);

  // Load user details
  const loadUserDetails = useCallback(async (userId: string) => {
    try {
      const data = await apiGet(`/api/admin/memory/users/${userId}`, { showErrorToast: false });
      setSelectedUser(data);
      setShowUserDetails(true);
    } catch (error) {
      logger.error('Failed to load user details:', error);
      toast.error('   ');
    }
  }, []);

  // Load learning analytics
  const loadLearningAnalytics = useCallback(async () => {
    try {
      const data = await apiGet('/api/admin/memory/learning-analytics?days=30', { showErrorToast: false });
      setLearningAnalytics(data);
    } catch (error) {
      logger.error('Failed to load learning analytics:', error);
    }
  }, []);

  // Load case analytics
  const loadCaseAnalytics = useCallback(async () => {
    try {
      const data = await apiGet('/api/admin/memory/case-analytics', { showErrorToast: false });
      setCaseAnalytics(data);
    } catch (error) {
      logger.error('Failed to load case analytics:', error);
    }
  }, []);

  // Load agent analytics
  const loadAgentAnalytics = useCallback(async () => {
    try {
      const data = await apiGet('/api/admin/memory/agent-analytics?days=30', { showErrorToast: false });
      setAgentAnalytics(data);
    } catch (error) {
      logger.error('Failed to load agent analytics:', error);
    }
  }, []);

  // Initial load
  useEffect(() => {
    const loadAll = async () => {
      setLoading(true);
      await Promise.all([
        loadOverview(),
        loadUsers(),
        loadLearningAnalytics(),
        loadCaseAnalytics(),
        loadAgentAnalytics(),
      ]);
      setLoading(false);
    };
    loadAll();
  }, [loadOverview, loadUsers, loadLearningAnalytics, loadCaseAnalytics, loadAgentAnalytics]);

  // Reload users when filters change
  useEffect(() => {
    if (!loading) {
      loadUsers();
    }
  }, [page, sortBy, filterTier, loadUsers, loading]);

  // Reset user memory
  const handleResetUserMemory = async (userId: string, resetType: string) => {
    if (!window.confirm(`   ${resetType}   ?`)) return;

    try {
      const params: Record<string, boolean> = {};
      if (resetType === 'profile') params.reset_profile = true;
      if (resetType === 'learning') params.reset_learning = true;
      if (resetType === 'case_memory') params.reset_case_memory = true;

      await apiPost(`/api/admin/memory/users/${userId}/reset?${new URLSearchParams(params as any)}`, {});
      toast.success('  ');

      // Reload
      if (selectedUser?.user.id === userId) {
        loadUserDetails(userId);
      }
      loadOverview();
    } catch (error) {
      logger.error('Failed to reset memory:', error);
      toast.error('  ');
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="animate-spin text-purple-400" size={48} />
      </div>
    );
  }

  return (
    <div className="space-y-6" dir="rtl">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="p-3 bg-purple-500/20 rounded-xl">
            <Brain className="text-purple-400" size={28} />
          </div>
          <div>
            <h2 className="text-2xl font-bold text-white">  </h2>
            <p className="text-white/60 text-sm">     </p>
          </div>
        </div>
        <button
          onClick={() => {
            loadOverview();
            loadUsers();
            loadLearningAnalytics();
            loadCaseAnalytics();
            loadAgentAnalytics();
          }}
          className="flex items-center gap-2 px-4 py-2 bg-white/10 hover:bg-white/20 rounded-lg text-white transition-colors"
        >
          <RefreshCw size={18} />
          
        </button>
      </div>

      {/* Tabs */}
      <div className="flex gap-2 border-b border-white/10 pb-2">
        {[
          { id: 'overview', label: ' ', icon: BarChart3 },
          { id: 'users', label: '', icon: Users },
          { id: 'learning', label: '', icon: Sparkles },
          { id: 'cases', label: '', icon: FileText },
          { id: 'agents', label: '', icon: Zap },
        ].map((tab) => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id as any)}
            className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
              activeTab === tab.id
                ? 'bg-purple-500/20 text-purple-400'
                : 'text-white/60 hover:text-white hover:bg-white/10'
            }`}
          >
            <tab.icon size={18} />
            {tab.label}
          </button>
        ))}
      </div>

      {/* Overview Tab */}
      {activeTab === 'overview' && overview && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {/* Profile Coverage */}
          <div className="bg-gradient-to-br from-blue-500/20 to-cyan-500/20 rounded-2xl p-5 border border-blue-500/30">
            <div className="flex items-center gap-3 mb-4">
              <UserCircle className="text-blue-400" size={24} />
              <span className="text-white font-medium"> </span>
            </div>
            <div className="text-3xl font-bold text-white mb-1">
              {overview.overview.profile_coverage_percent}%
            </div>
            <p className="text-white/60 text-sm">
              {overview.overview.users_with_profile} / {overview.overview.total_users} 
            </p>
          </div>

          {/* Learning Users */}
          <div className="bg-gradient-to-br from-purple-500/20 to-pink-500/20 rounded-2xl p-5 border border-purple-500/30">
            <div className="flex items-center gap-3 mb-4">
              <Brain className="text-purple-400" size={24} />
              <span className="text-white font-medium">  </span>
            </div>
            <div className="text-3xl font-bold text-white mb-1">
              {overview.overview.users_with_learning}
            </div>
            <p className="text-white/60 text-sm">
               {overview.overview.total_users} 
            </p>
          </div>

          {/* Case Memory */}
          <div className="bg-gradient-to-br from-green-500/20 to-emerald-500/20 rounded-2xl p-5 border border-green-500/30">
            <div className="flex items-center gap-3 mb-4">
              <Database className="text-green-400" size={24} />
              <span className="text-white font-medium"> </span>
            </div>
            <div className="text-3xl font-bold text-white mb-1">
              {overview.case_memory.memory_coverage_percent}%
            </div>
            <p className="text-white/60 text-sm">
              {overview.case_memory.cases_with_memory} / {overview.case_memory.total_cases} 
            </p>
          </div>

          {/* Chat Stats */}
          <div className="bg-gradient-to-br from-amber-500/20 to-orange-500/20 rounded-2xl p-5 border border-amber-500/30">
            <div className="flex items-center gap-3 mb-4">
              <MessageSquare className="text-amber-400" size={24} />
              <span className="text-white font-medium"></span>
            </div>
            <div className="text-3xl font-bold text-white mb-1">
              {overview.chat_stats.total_messages.toLocaleString()}
            </div>
            <p className="text-white/60 text-sm">
              {overview.chat_stats.total_sessions} , {overview.chat_stats.avg_messages_per_session} 
            </p>
          </div>

          {/* Learning Level Distribution */}
          <div className="col-span-2 bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4 flex items-center gap-2">
              <Award className="text-purple-400" size={20} />
                
            </h3>
            <div className="space-y-2">
              {Object.entries(overview.learning_levels).map(([level, count]) => {
                const levelInfo = LEARNING_LEVELS[parseInt(level)] || { name: `Level ${level}`, color: 'gray' };
                const percent = (count / Math.max(overview.overview.total_users, 1)) * 100;
                return (
                  <div key={level} className="flex items-center gap-3">
                    <span className="w-24 text-white/70 text-sm">L{level}: {levelInfo.name}</span>
                    <div className="flex-1 h-3 bg-white/10 rounded-full overflow-hidden">
                      <div
                        className={`h-full bg-${levelInfo.color}-500`}
                        style={{ width: `${percent}%`, backgroundColor: `var(--${levelInfo.color}-500, #8b5cf6)` }}
                      />
                    </div>
                    <span className="w-12 text-white/70 text-sm text-left">{count}</span>
                  </div>
                );
              })}
            </div>
          </div>

          {/* Tier Distribution */}
          <div className="col-span-2 bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4 flex items-center gap-2">
              <Shield className="text-amber-400" size={20} />
               
            </h3>
            <div className="grid grid-cols-2 sm:grid-cols-4 gap-4">
              {Object.entries(overview.tier_distribution).map(([tier, count]) => (
                <div key={tier} className="bg-white/5 rounded-xl p-4 text-center">
                  <div className={`text-2xl font-bold ${TIER_COLORS[tier] || 'text-white'}`}>
                    {count}
                  </div>
                  <div className="text-white/60 text-sm capitalize">{tier}</div>
                </div>
              ))}
            </div>
          </div>

          {/* Profile Completeness */}
          <div className="col-span-full bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4 flex items-center gap-2">
              <Activity className="text-cyan-400" size={20} />
               
            </h3>
            <div className="grid grid-cols-3 gap-4">
              <div className="bg-green-500/10 rounded-xl p-4 text-center border border-green-500/30">
                <CheckCircle className="mx-auto text-green-400 mb-2" size={32} />
                <div className="text-2xl font-bold text-green-400">{overview.profile_completeness.complete}</div>
                <div className="text-white/60 text-sm"> (80%+)</div>
              </div>
              <div className="bg-amber-500/10 rounded-xl p-4 text-center border border-amber-500/30">
                <AlertTriangle className="mx-auto text-amber-400 mb-2" size={32} />
                <div className="text-2xl font-bold text-amber-400">{overview.profile_completeness.partial}</div>
                <div className="text-white/60 text-sm"> (30-80%)</div>
              </div>
              <div className="bg-red-500/10 rounded-xl p-4 text-center border border-red-500/30">
                <UserCircle className="mx-auto text-red-400 mb-2" size={32} />
                <div className="text-2xl font-bold text-red-400">{overview.profile_completeness.empty}</div>
                <div className="text-white/60 text-sm"> (&lt;30%)</div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Users Tab */}
      {activeTab === 'users' && (
        <div className="space-y-4">
          {/* Filters */}
          <div className="flex items-center gap-4 bg-white/5 rounded-xl p-4">
            <Filter className="text-white/50" size={20} />
            <select
              value={filterTier}
              onChange={(e) => { setFilterTier(e.target.value); setPage(1); }}
              className="bg-white/10 border border-white/20 rounded-lg px-3 py-2 text-white"
            >
              <option value=""> </option>
              <option value="free">Free</option>
              <option value="basic">Basic</option>
              <option value="professional">Professional</option>
              <option value="enterprise">Enterprise</option>
            </select>
            <select
              value={sortBy}
              onChange={(e) => { setSortBy(e.target.value); setPage(1); }}
              className="bg-white/10 border border-white/20 rounded-lg px-3 py-2 text-white"
            >
              <option value="last_active"> </option>
              <option value="memory_score"> </option>
              <option value="learning_level"> </option>
              <option value="name"></option>
            </select>
          </div>

          {/* Users Table */}
          <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl border border-white/10 overflow-hidden">
            <table className="w-full">
              <thead className="bg-white/5">
                <tr>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"></th>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"></th>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"> </th>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"> </th>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"></th>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"></th>
                  <th className="text-right text-white/70 px-4 py-3 font-medium"></th>
                </tr>
              </thead>
              <tbody>
                {users.map((user) => (
                  <tr key={user.id} className="border-t border-white/5 hover:bg-white/5">
                    <td className="px-4 py-3">
                      <div className="flex items-center gap-3">
                        <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                          user.has_profile ? 'bg-green-500/20' : 'bg-white/10'
                        }`}>
                          <UserCircle className={user.has_profile ? 'text-green-400' : 'text-white/50'} size={20} />
                        </div>
                        <div>
                          <div className="text-white font-medium">{user.name}</div>
                          <div className="text-white/50 text-sm">{user.email}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-4 py-3">
                      <span className={`px-2 py-1 rounded-lg text-sm ${TIER_COLORS[user.tier]} bg-white/10`}>
                        {user.tier}
                      </span>
                    </td>
                    <td className="px-4 py-3">
                      <div className="flex items-center gap-2">
                        <span className="text-white">{user.learning_level}</span>
                        <span className="text-white/50 text-sm">
                          {LEARNING_LEVELS[user.learning_level]?.name || ''}
                        </span>
                      </div>
                    </td>
                    <td className="px-4 py-3">
                      <div className="flex items-center gap-2">
                        <div className="w-16 h-2 bg-white/10 rounded-full overflow-hidden">
                          <div
                            className={`h-full ${
                              user.memory_score >= 80 ? 'bg-green-500' :
                              user.memory_score >= 30 ? 'bg-amber-500' : 'bg-red-500'
                            }`}
                            style={{ width: `${user.memory_score}%` }}
                          />
                        </div>
                        <span className="text-white/70 text-sm">{user.memory_score}%</span>
                      </div>
                    </td>
                    <td className="px-4 py-3 text-white/70">{user.case_count}</td>
                    <td className="px-4 py-3 text-white/70">{user.message_count}</td>
                    <td className="px-4 py-3">
                      <button
                        onClick={() => loadUserDetails(user.id)}
                        className="p-2 hover:bg-white/10 rounded-lg text-white/70 hover:text-white transition-colors"
                        title=" "
                      >
                        <Eye size={18} />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {/* Pagination */}
            {totalPages > 1 && (
              <div className="flex items-center justify-center gap-2 p-4 border-t border-white/10">
                <button
                  onClick={() => setPage(p => Math.max(1, p - 1))}
                  disabled={page === 1}
                  className="px-3 py-1 bg-white/10 rounded-lg text-white disabled:opacity-50"
                >
                  
                </button>
                <span className="text-white/70">
                   {page}  {totalPages}
                </span>
                <button
                  onClick={() => setPage(p => Math.min(totalPages, p + 1))}
                  disabled={page === totalPages}
                  className="px-3 py-1 bg-white/10 rounded-lg text-white disabled:opacity-50"
                >
                  
                </button>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Learning Tab */}
      {activeTab === 'learning' && learningAnalytics && (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {/* Totals */}
          <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"> </h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-white/70">  </span>
                <span className="text-white font-medium">{learningAnalytics.totals.users_with_learning}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70">" </span>
                <span className="text-white font-medium">{learningAnalytics.totals.total_interactions.toLocaleString()}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{learningAnalytics.totals.total_corrections}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{learningAnalytics.totals.total_feedback}</span>
              </div>
            </div>
          </div>

          {/* Averages */}
          <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"></h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-white/70">  </span>
                <span className="text-white font-medium">{learningAnalytics.averages.avg_learning_level}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"> </span>
                <span className="text-white font-medium">{learningAnalytics.averages.avg_interactions_per_user}</span>
              </div>
            </div>
          </div>

          {/* Effectiveness */}
          <div className="col-span-full bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"> </h3>
            <div className="grid grid-cols-2 gap-4">
              <div className="bg-white/5 rounded-xl p-4 text-center">
                <div className="text-3xl font-bold text-cyan-400">{learningAnalytics.effectiveness.correction_rate}%</div>
                <div className="text-white/60 text-sm"> </div>
              </div>
              <div className="bg-white/5 rounded-xl p-4 text-center">
                <div className="text-3xl font-bold text-purple-400">{learningAnalytics.effectiveness.feedback_rate}%</div>
                <div className="text-white/60 text-sm"> </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Cases Tab */}
      {activeTab === 'cases' && caseAnalytics && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {/* Totals */}
          <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"> </h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-white/70">" </span>
                <span className="text-white font-medium">{caseAnalytics.totals.cases}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70">  </span>
                <span className="text-white font-medium">{caseAnalytics.totals.cases_with_memory}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"> </span>
                <span className="text-green-400 font-medium">{caseAnalytics.coverage.memory_coverage_percent}%</span>
              </div>
            </div>
          </div>

          {/* Analysis Stats */}
          <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"> </h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{caseAnalytics.totals.claims.toLocaleString()}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-red-400 font-medium">{caseAnalytics.totals.contradictions}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-cyan-400 font-medium">{caseAnalytics.totals.entities}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{caseAnalytics.totals.tokens_used.toLocaleString()}</span>
              </div>
            </div>
          </div>

          {/* Averages */}
          <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"> </h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{caseAnalytics.averages.claims_per_case}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{caseAnalytics.averages.contradictions_per_case}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{caseAnalytics.averages.entities_per_case}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-white/70"></span>
                <span className="text-white font-medium">{caseAnalytics.averages.tokens_per_case.toLocaleString()}</span>
              </div>
            </div>
          </div>

          {/* Status Distribution */}
          <div className="col-span-full bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
            <h3 className="text-lg font-bold text-white mb-4"> </h3>
            <div className="grid grid-cols-2 sm:grid-cols-4 gap-4">
              {Object.entries(caseAnalytics.status_distribution).map(([status, count]) => (
                <div key={status} className="bg-white/5 rounded-xl p-4 text-center">
                  <div className="text-2xl font-bold text-white">{count}</div>
                  <div className="text-white/60 text-sm">{status}</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Agents Tab */}
      {activeTab === 'agents' && agentAnalytics && (
        <div className="space-y-4">
          <div className="bg-white/5 rounded-xl p-4 text-center">
            <span className="text-white/70">
              : {agentAnalytics.period_days}  |
               : {agentAnalytics.cases_analyzed}
            </span>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {Object.entries(agentAnalytics.agent_stats).map(([agent, stats]) => (
              <div key={agent} className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-5 border border-white/10">
                <div className="flex items-center gap-3 mb-4">
                  <Zap className="text-amber-400" size={24} />
                  <span className="text-white font-bold capitalize">{agent}</span>
                </div>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-white/70"></span>
                    <span className="text-white font-medium">{stats.runs}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-white/70"></span>
                    <span className="text-green-400 font-medium">{stats.success}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-white/70"> </span>
                    <span className={`font-medium ${stats.success_rate >= 90 ? 'text-green-400' : stats.success_rate >= 70 ? 'text-amber-400' : 'text-red-400'}`}>
                      {stats.success_rate}%
                    </span>
                  </div>
                  {stats.total_pages !== undefined && (
                    <div className="flex justify-between">
                      <span className="text-white/70"></span>
                      <span className="text-white font-medium">{stats.total_pages.toLocaleString()}</span>
                    </div>
                  )}
                  {stats.total_claims !== undefined && (
                    <div className="flex justify-between">
                      <span className="text-white/70"></span>
                      <span className="text-white font-medium">{stats.total_claims.toLocaleString()}</span>
                    </div>
                  )}
                  {stats.total_entities !== undefined && (
                    <div className="flex justify-between">
                      <span className="text-white/70"></span>
                      <span className="text-white font-medium">{stats.total_entities}</span>
                    </div>
                  )}
                  {stats.total_found !== undefined && (
                    <div className="flex justify-between">
                      <span className="text-white/70"></span>
                      <span className="text-white font-medium">{stats.total_found}</span>
                    </div>
                  )}
                  {stats.total_insights !== undefined && (
                    <div className="flex justify-between">
                      <span className="text-white/70"></span>
                      <span className="text-white font-medium">{stats.total_insights}</span>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* User Details Modal */}
      {showUserDetails && selectedUser && (
        <div className="fixed inset-0 bg-black/80 flex items-center justify-center z-50 p-4">
          <div className="bg-slate-900 rounded-2xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
            {/* Header */}
            <div className="sticky top-0 bg-slate-900 border-b border-white/10 p-4 flex items-center justify-between">
              <div className="flex items-center gap-3">
                <UserCircle className="text-purple-400" size={32} />
                <div>
                  <h3 className="text-xl font-bold text-white">{selectedUser.user.name}</h3>
                  <p className="text-white/60 text-sm">{selectedUser.user.email}</p>
                </div>
              </div>
              <button
                onClick={() => setShowUserDetails(false)}
                className="p-2 hover:bg-white/10 rounded-lg text-white/70 hover:text-white"
              >
                
              </button>
            </div>

            <div className="p-6 space-y-6">
              {/* User Info */}
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <div className="bg-white/5 rounded-xl p-4 text-center">
                  <div className={`text-lg font-bold ${TIER_COLORS[selectedUser.user.tier]}`}>
                    {selectedUser.user.tier}
                  </div>
                  <div className="text-white/60 text-sm"></div>
                </div>
                <div className="bg-white/5 rounded-xl p-4 text-center">
                  <div className="text-lg font-bold text-white">{selectedUser.learning.level}</div>
                  <div className="text-white/60 text-sm"> </div>
                </div>
                <div className="bg-white/5 rounded-xl p-4 text-center">
                  <div className="text-lg font-bold text-white">{selectedUser.profile?.completeness || 0}%</div>
                  <div className="text-white/60 text-sm"> </div>
                </div>
                <div className="bg-white/5 rounded-xl p-4 text-center">
                  <div className="text-lg font-bold text-white">{selectedUser.cases.length}</div>
                  <div className="text-white/60 text-sm"></div>
                </div>
              </div>

              {/* Profile Details */}
              {selectedUser.profile && (
                <div className="bg-white/5 rounded-xl p-4">
                  <h4 className="text-white font-medium mb-3"> </h4>
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div>
                      <span className="text-white/50">: </span>
                      <span className="text-white">{selectedUser.profile.professional_identity.role || ' '}</span>
                    </div>
                    <div>
                      <span className="text-white/50">: </span>
                      <span className="text-white">
                        {selectedUser.profile.professional_identity.specializations?.join(', ') || ' '}
                      </span>
                    </div>
                    <div>
                      <span className="text-white/50"> : </span>
                      <span className="text-white">
                        {selectedUser.profile.professional_relationships.judges?.length || 0}
                      </span>
                    </div>
                    <div>
                      <span className="text-white/50">" : </span>
                      <span className="text-white">
                        {selectedUser.profile.professional_relationships.opposing_lawyers?.length || 0}
                      </span>
                    </div>
                  </div>

                  {/* Opposing Lawyers List */}
                  {selectedUser.profile.professional_relationships.opposing_lawyers?.length > 0 && (
                    <div className="mt-4 pt-4 border-t border-white/10">
                      <h5 className="text-white/70 text-sm mb-2"> " :</h5>
                      <div className="flex flex-wrap gap-2">
                        {selectedUser.profile.professional_relationships.opposing_lawyers.map((lawyer: any, idx: number) => (
                          <span
                            key={idx}
                            className={`px-2 py-1 rounded-lg text-xs ${
                              lawyer.sentiment === 'positive' ? 'bg-green-500/20 text-green-400' :
                              lawyer.sentiment === 'negative' ? 'bg-red-500/20 text-red-400' :
                              'bg-purple-500/20 text-purple-300'
                            }`}
                            title={`${lawyer.style || ''} | ${lawyer.notes || ''} | : ${lawyer.cases_count || 1}`}
                          >
                            {lawyer.name}
                            {lawyer.cases_count > 1 && <span className="text-white/50 mr-1">({lawyer.cases_count})</span>}
                          </span>
                        ))}
                      </div>
                    </div>
                  )}

                  {/* Judges List */}
                  {selectedUser.profile.professional_relationships.judges?.length > 0 && (
                    <div className="mt-4 pt-4 border-t border-white/10">
                      <h5 className="text-white/70 text-sm mb-2">  :</h5>
                      <div className="flex flex-wrap gap-2">
                        {selectedUser.profile.professional_relationships.judges.map((judge: any, idx: number) => (
                          <span
                            key={idx}
                            className="px-2 py-1 bg-amber-500/20 text-amber-300 rounded-lg text-xs"
                            title={`${judge.court || ''} | ${judge.notes || ''}`}
                          >
                            {judge.name}
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              )}

              {/* Token & Cost */}
              <div className="grid grid-cols-2 gap-4">
                <div className="bg-white/5 rounded-xl p-4">
                  <h4 className="text-white font-medium mb-3"> </h4>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-white/50">"</span>
                      <span className="text-white">{selectedUser.token_usage.total.toLocaleString()}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-white/50"></span>
                      <span className="text-white">{selectedUser.token_usage.monthly.toLocaleString()}</span>
                    </div>
                  </div>
                </div>
                <div className="bg-white/5 rounded-xl p-4">
                  <h4 className="text-white font-medium mb-3"></h4>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-white/50">"</span>
                      <span className="text-white">${(selectedUser.cost.total_cents / 100000).toFixed(4)}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-white/50"></span>
                      <span className="text-white">${(selectedUser.cost.monthly_cents / 100000).toFixed(4)}</span>
                    </div>
                  </div>
                </div>
              </div>

              {/* Cases */}
              <div className="bg-white/5 rounded-xl p-4">
                <h4 className="text-white font-medium mb-3"> ({selectedUser.cases.length})</h4>
                <div className="max-h-48 overflow-y-auto space-y-2">
                  {selectedUser.cases.map((c) => (
                    <div key={c.id} className="flex items-center justify-between bg-white/5 rounded-lg p-3">
                      <div>
                        <div className="text-white font-medium">{c.name}</div>
                        <div className="text-white/50 text-xs">
                          {c.claims_count}  | {c.contradictions_count}  | {c.entities_count} 
                        </div>
                      </div>
                      <div className={`px-2 py-1 rounded text-xs ${
                        c.status === 'completed' ? 'bg-green-500/20 text-green-400' :
                        c.status === 'analyzing' ? 'bg-amber-500/20 text-amber-400' :
                        'bg-white/10 text-white/70'
                      }`}>
                        {c.status}
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Reset Actions */}
              <div className="bg-red-500/10 border border-red-500/30 rounded-xl p-4">
                <h4 className="text-red-400 font-medium mb-3 flex items-center gap-2">
                  <AlertTriangle size={18} />
                   
                </h4>
                <div className="flex flex-wrap gap-2">
                  <button
                    onClick={() => handleResetUserMemory(selectedUser.user.id, 'profile')}
                    className="px-3 py-2 bg-red-500/20 hover:bg-red-500/30 text-red-400 rounded-lg text-sm transition-colors"
                  >
                     
                  </button>
                  <button
                    onClick={() => handleResetUserMemory(selectedUser.user.id, 'learning')}
                    className="px-3 py-2 bg-red-500/20 hover:bg-red-500/30 text-red-400 rounded-lg text-sm transition-colors"
                  >
                     
                  </button>
                  <button
                    onClick={() => handleResetUserMemory(selectedUser.user.id, 'case_memory')}
                    className="px-3 py-2 bg-red-500/20 hover:bg-red-500/30 text-red-400 rounded-lg text-sm transition-colors"
                  >
                      
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
