/**
 * Cache Management Dashboard - Cache Control UI
 *  P1 FIX: UI for managing application cache
 */

import React, { useState, useEffect } from 'react';
import {
  Database, RefreshCw, Trash2, Loader2, AlertCircle,
  TrendingUp, Zap
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiDelete } from '../../utils/api';
import { logger } from '../../utils/logger';

interface CacheStats {
  total_keys: number;
  memory_usage: number;
  hit_rate: number;
  miss_rate: number;
  evictions: number;
  by_type: Record<string, number>;
}

const CacheManagementDashboard = () => {
  const [stats, setStats] = useState<CacheStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [clearing, setClearing] = useState(false);
  const [keys, setKeys] = useState<string[]>([]);
  const [showKeys, setShowKeys] = useState(false);

  //  PERF FIX: Increased interval from 10s to 30s, added visibility check
  useEffect(() => {
    loadStats();

    const poll = () => {
      if (!document.hidden) {
        loadStats();
      }
    };

    const interval = setInterval(poll, 30000); // 30 seconds instead of 10
    return () => clearInterval(interval);
  }, []);

  const loadStats = async () => {
    try {
      const data = await apiGet('/api/cache/stats', { showErrorToast: false });
      setStats(data.stats);
      if (data.keys) {
        setKeys(data.keys);
      }
    } catch (error) {
      logger.error('Failed to load cache stats:', error);
      toast.error('   cache');
    } finally {
      setLoading(false);
    }
  };

  const handleClearCache = async (pattern?: string) => {
    try {
      setClearing(true);
      // Cache API uses DELETE method
      await apiDelete('/api/cache/clear', { showErrorToast: false });
      toast.success('Cache  ');
      await loadStats();
    } catch (error) {
      logger.error('Failed to clear cache:', error);
      toast.error('  cache');
    } finally {
      setClearing(false);
    }
  };

  const formatBytes = (bytes: number) => {
    if (bytes === 0) return '0 B';
    const k = 1024;
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
  };

  if (loading && !stats) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="animate-spin text-blue-400" size={32} />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-6 border border-white/10">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-3">
            <Database className="text-blue-400" size={32} />
            <div>
              <h2 className="text-2xl font-bold text-white">Cache Management</h2>
              <p className="text-blue-300 text-sm"> cache  </p>
            </div>
          </div>
          <button
            onClick={loadStats}
            disabled={loading}
            className="px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 text-white rounded-lg flex items-center gap-2"
          >
            <RefreshCw className={loading ? 'animate-spin' : ''} size={16} />
            
          </button>
        </div>

        {/* Stats Cards */}
        {stats && (
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="flex items-center gap-2 mb-2">
                <Database className="text-blue-400" size={20} />
                <div className="text-slate-400 text-sm">" </div>
              </div>
              <div className="text-2xl font-bold text-white">{(stats.total_keys ?? 0).toLocaleString()}</div>
            </div>
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="flex items-center gap-2 mb-2">
                <TrendingUp className="text-green-400" size={20} />
                <div className="text-slate-400 text-sm">Hit Rate</div>
              </div>
              <div className="text-2xl font-bold text-green-400">{((stats.hit_rate ?? 0) * 100).toFixed(1)}%</div>
            </div>
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="flex items-center gap-2 mb-2">
                <AlertCircle className="text-red-400" size={20} />
                <div className="text-slate-400 text-sm">Miss Rate</div>
              </div>
              <div className="text-2xl font-bold text-red-400">{((stats.miss_rate ?? 0) * 100).toFixed(1)}%</div>
            </div>
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="flex items-center gap-2 mb-2">
                <Zap className="text-yellow-400" size={20} />
                <div className="text-slate-400 text-sm"></div>
              </div>
              <div className="text-2xl font-bold text-yellow-400">{formatBytes(stats.memory_usage ?? 0)}</div>
            </div>
          </div>
        )}
      </div>

      {/* Cache by Type */}
      {stats && stats.by_type && Object.keys(stats.by_type).length > 0 && (
        <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
          <h3 className="text-xl font-bold text-white mb-4">Cache  </h3>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            {Object.entries(stats.by_type).map(([type, count]) => (
              <div key={type} className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
                <div className="text-slate-400 text-sm mb-1">{type}</div>
                <div className="text-2xl font-bold text-white">{((count as number) ?? 0).toLocaleString()}</div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Actions */}
      <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
        <h3 className="text-xl font-bold text-white mb-4"></h3>
        <div className="flex flex-wrap gap-4">
          <button
            onClick={() => handleClearCache()}
            disabled={clearing}
            className="px-6 py-3 bg-red-600 hover:bg-red-700 disabled:bg-slate-600 text-white rounded-lg flex items-center gap-2"
          >
            {clearing ? (
              <>
                <Loader2 className="animate-spin" size={20} />
                ...
              </>
            ) : (
              <>
                <Trash2 size={20} />
                  -Cache
              </>
            )}
          </button>
          <button
            onClick={() => setShowKeys(!showKeys)}
            className="px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white rounded-lg flex items-center gap-2"
          >
            <Database size={20} />
            {showKeys ? ' ' : ' '}
          </button>
        </div>
      </div>

      {/* Keys List */}
      {showKeys && keys.length > 0 && (
        <div className="bg-slate-800 rounded-xl p-6 border border-white/10">
          <h3 className="text-xl font-bold text-white mb-4"> Cache ({keys.length})</h3>
          <div className="max-h-96 overflow-y-auto">
            <div className="space-y-2">
              {keys.map((key, idx) => (
                <div
                  key={idx}
                  className="bg-slate-700/50 rounded-lg p-3 border border-slate-600 font-mono text-sm text-slate-300"
                >
                  {key}
                </div>
              ))}
            </div>
          </div>
        </div>
      )}

      {showKeys && keys.length === 0 && (
        <div className="bg-slate-800 rounded-xl p-6 border border-white/10 text-center">
          <AlertCircle className="text-slate-400 mx-auto mb-3" size={48} />
          <p className="text-slate-300">  cache</p>
        </div>
      )}
    </div>
  );
};

export default CacheManagementDashboard;

