// ============================================================================
//  User Entitlements Dashboard -  
// ============================================================================

import React, { useState, useCallback } from 'react';
import {
  User,
  Search,
  Shield,
  RefreshCw,
  Save,
  Trash2,
  Check,
  X,
  AlertTriangle,
  Zap,
  Clock,
  TrendingUp,
  Settings,
  Plus,
  Minus
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPost } from '../../utils/api';

// Types
interface UserEntitlements {
  user_id: string;
  email: string;
  tier: string;
  monthly_tokens_limit: number;
  monthly_tokens_used: number;
  monthly_tokens_remaining: number;
  monthly_cases_limit: number | null;
  enabled_services: string[];
  service_caps: Record<string, Record<string, number>>;
  source_policy_id: string | null;
  has_overrides: boolean;
  usage_by_service: Record<string, Record<string, { used: number; limit: number | null; remaining: number | null }>>;
}

interface ServiceInfo {
  name: string;
  display_name: string;
  description: string;
  category: string;
  caps: string[];
}

// Format numbers
const formatNumber = (n: number): string => {
  if (n >= 1000000) return `${(n / 1000000).toFixed(1)}M`;
  if (n >= 1000) return `${(n / 1000).toFixed(0)}K`;
  return n.toString();
};

// Progress bar component
const ProgressBar: React.FC<{ used: number; limit: number; label?: string }> = ({ used, limit, label }) => {
  const pct = limit > 0 ? Math.min(100, (used / limit) * 100) : 0;
  const color = pct > 90 ? 'bg-red-500' : pct > 70 ? 'bg-yellow-500' : 'bg-green-500';

  return (
    <div className="space-y-1">
      {label && <div className="text-xs text-gray-400">{label}</div>}
      <div className="h-2 bg-gray-700 rounded-full overflow-hidden">
        <div className={`h-full ${color} transition-all`} style={{ width: `${pct}%` }} />
      </div>
      <div className="text-xs text-gray-500">
        {formatNumber(used)} / {formatNumber(limit)} ({pct.toFixed(0)}%)
      </div>
    </div>
  );
};

// Main Component
const UserEntitlementsDashboard: React.FC = () => {
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [userEntitlements, setUserEntitlements] = useState<UserEntitlements | null>(null);
  const [services, setServices] = useState<ServiceInfo[]>([]);

  // Override form state
  const [overrideTokenQuota, setOverrideTokenQuota] = useState<number | null>(null);
  const [overrideMode, setOverrideMode] = useState<'merge' | 'replace'>('merge');
  const [addServices, setAddServices] = useState<string[]>([]);
  const [removeServices, setRemoveServices] = useState<string[]>([]);
  const [overrideCaps, setOverrideCaps] = useState<Record<string, Record<string, number>>>({});

  // Fetch user entitlements
  const searchUser = useCallback(async () => {
    if (!searchQuery.trim()) {
      toast.error('   ');
      return;
    }

    try {
      setLoading(true);
      const [entitlements, serviceList] = await Promise.all([
        apiGet(`/admin/subscriptions/users/${searchQuery}/entitlements`),
        apiGet('/admin/subscriptions/services')
      ]);
      setUserEntitlements(entitlements);
      setServices(serviceList || []);

      // Reset override form
      setOverrideTokenQuota(null);
      setOverrideMode('merge');
      setAddServices([]);
      setRemoveServices([]);
      setOverrideCaps({});
    } catch (error: any) {
      console.error('Failed to fetch user:', error);
      toast.error(error?.message || '  ');
      setUserEntitlements(null);
    } finally {
      setLoading(false);
    }
  }, [searchQuery]);

  // Save overrides
  const saveOverrides = async () => {
    if (!userEntitlements) return;

    try {
      setLoading(true);

      const customLimits: any = {};
      if (overrideMode === 'replace' && (addServices.length > 0 || removeServices.length > 0)) {
        customLimits.enabled_services_mode = 'replace';
        customLimits.enabled_services = addServices;
      } else {
        if (addServices.length > 0) customLimits.add_services = addServices;
        if (removeServices.length > 0) customLimits.remove_services = removeServices;
      }
      if (Object.keys(overrideCaps).length > 0) {
        customLimits.service_caps = overrideCaps;
      }

      await apiPost(`/admin/subscriptions/users/${userEntitlements.user_id}/override`, {
        monthly_token_quota: overrideTokenQuota,
        custom_limits: Object.keys(customLimits).length > 0 ? customLimits : null
      });

      toast.success(' ');
      searchUser(); // Refresh
    } catch (error) {
      console.error('Failed to save overrides:', error);
      toast.error('  ');
    } finally {
      setLoading(false);
    }
  };

  // Reset usage
  const resetUsage = async (serviceName?: string) => {
    if (!userEntitlements) return;

    try {
      const url = serviceName
        ? `/admin/subscriptions/users/${userEntitlements.user_id}/reset-usage?service_name=${serviceName}`
        : `/admin/subscriptions/users/${userEntitlements.user_id}/reset-usage`;
      await apiPost(url, {});
      toast.success(serviceName ? ` ${serviceName} ` : '  ');
      searchUser(); // Refresh
    } catch (error) {
      console.error('Failed to reset usage:', error);
      toast.error('  ');
    }
  };

  // Toggle service in add/remove lists
  const toggleServiceOverride = (serviceName: string, action: 'add' | 'remove') => {
    if (action === 'add') {
      if (addServices.includes(serviceName)) {
        setAddServices(prev => prev.filter(s => s !== serviceName));
      } else {
        setAddServices(prev => [...prev, serviceName]);
        setRemoveServices(prev => prev.filter(s => s !== serviceName));
      }
    } else {
      if (removeServices.includes(serviceName)) {
        setRemoveServices(prev => prev.filter(s => s !== serviceName));
      } else {
        setRemoveServices(prev => [...prev, serviceName]);
        setAddServices(prev => prev.filter(s => s !== serviceName));
      }
    }
  };

  // Update cap override
  const updateCapOverride = (serviceName: string, capKey: string, value: number | null) => {
    setOverrideCaps(prev => {
      const newCaps = { ...prev };
      if (value === null) {
        if (newCaps[serviceName]) {
          delete newCaps[serviceName][capKey];
          if (Object.keys(newCaps[serviceName]).length === 0) {
            delete newCaps[serviceName];
          }
        }
      } else {
        if (!newCaps[serviceName]) {
          newCaps[serviceName] = {};
        }
        newCaps[serviceName][capKey] = value;
      }
      return newCaps;
    });
  };

  return (
    <div className="p-6 space-y-6 bg-gray-900 min-h-screen text-white" dir="rtl">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold flex items-center gap-2">
          <User className="w-6 h-6 text-purple-400" />
           
        </h1>
        <p className="text-gray-400 mt-1">
               .
        </p>
      </div>

      {/* Search */}
      <div className="flex gap-4">
        <div className="flex-1 relative">
          <Search className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
          <input
            type="text"
            value={searchQuery}
            onChange={e => setSearchQuery(e.target.value)}
            onKeyDown={e => e.key === 'Enter' && searchUser()}
            placeholder="     ..."
            className="w-full bg-gray-800 border border-gray-700 rounded-lg pr-10 pl-4 py-3 text-white placeholder-gray-500"
          />
        </div>
        <button
          onClick={searchUser}
          disabled={loading}
          className="flex items-center gap-2 px-6 py-3 bg-blue-600 hover:bg-blue-500 disabled:bg-gray-600 rounded-lg font-medium"
        >
          {loading ? <RefreshCw className="w-5 h-5 animate-spin" /> : <Search className="w-5 h-5" />}
          
        </button>
      </div>

      {/* User Details */}
      {userEntitlements && (
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* User Info & Usage */}
          <div className="lg:col-span-2 space-y-6">
            {/* Basic Info */}
            <div className="bg-gray-800 rounded-xl p-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-xl font-semibold">{userEntitlements.email}</h2>
                <div className="flex items-center gap-2">
                  <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                    userEntitlements.tier === 'admin' ? 'bg-purple-600' :
                    userEntitlements.tier === 'enterprise' ? 'bg-blue-600' :
                    userEntitlements.tier === 'pro' ? 'bg-green-600' :
                    'bg-gray-600'
                  }`}>
                    {userEntitlements.tier}
                  </span>
                  {userEntitlements.has_overrides && (
                    <span className="px-2 py-1 rounded bg-yellow-600/30 text-yellow-400 text-xs">
                       
                    </span>
                  )}
                </div>
              </div>

              {/* Token Usage */}
              <div className="mb-6">
                <div className="flex items-center justify-between mb-2">
                  <span className="font-medium">  </span>
                  <button
                    onClick={() => resetUsage()}
                    className="text-xs text-blue-400 hover:text-blue-300 flex items-center gap-1"
                  >
                    <RefreshCw className="w-3 h-3" />
                     
                  </button>
                </div>
                <ProgressBar
                  used={userEntitlements.monthly_tokens_used}
                  limit={userEntitlements.monthly_tokens_limit}
                />
              </div>

              {/* Enabled Services */}
              <div>
                <div className="font-medium mb-2">  ({userEntitlements.enabled_services.length})</div>
                <div className="flex flex-wrap gap-2">
                  {userEntitlements.enabled_services.map(service => (
                    <span key={service} className="px-2 py-1 bg-gray-700 rounded text-sm flex items-center gap-1">
                      <Check className="w-3 h-3 text-green-400" />
                      {service}
                    </span>
                  ))}
                </div>
              </div>
            </div>

            {/* Service Usage */}
            {Object.keys(userEntitlements.usage_by_service).length > 0 && (
              <div className="bg-gray-800 rounded-xl p-6">
                <h3 className="text-lg font-semibold mb-4"> -</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {Object.entries(userEntitlements.usage_by_service).map(([serviceName, caps]) => (
                    <div key={serviceName} className="bg-gray-700 rounded-lg p-4">
                      <div className="flex items-center justify-between mb-2">
                        <span className="font-medium">{serviceName}</span>
                        <button
                          onClick={() => resetUsage(serviceName)}
                          className="text-xs text-gray-400 hover:text-white"
                        >
                          
                        </button>
                      </div>
                      {Object.entries(caps).map(([capKey, usage]) => (
                        <div key={capKey} className="mt-2">
                          <ProgressBar
                            used={usage.used}
                            limit={usage.limit || 999999}
                            label={capKey}
                          />
                        </div>
                      ))}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>

          {/* Override Panel */}
          <div className="space-y-6">
            <div className="bg-gray-800 rounded-xl p-6">
              <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
                <Settings className="w-5 h-5 text-yellow-400" />
                 
              </h3>

              {/* Token Quota Override */}
              <div className="mb-4">
                <label className="block text-sm text-gray-400 mb-1">
                    ()
                </label>
                <input
                  type="number"
                  value={overrideTokenQuota ?? ''}
                  onChange={e => setOverrideTokenQuota(e.target.value ? parseInt(e.target.value) : null)}
                  placeholder="    "
                  className="w-full bg-gray-700 border border-gray-600 rounded-lg px-3 py-2 text-white"
                />
                <p className="text-xs text-gray-500 mt-1">
                      
                </p>
              </div>

              {/* Services Override */}
              <div className="mb-4">
                <label className="block text-sm text-gray-400 mb-2">
                   
                </label>
                <div className="max-h-40 overflow-y-auto space-y-1">
                  {services.map(service => {
                    const isEnabled = userEntitlements.enabled_services.includes(service.name);
                    const inAdd = addServices.includes(service.name);
                    const inRemove = removeServices.includes(service.name);

                    return (
                      <div key={service.name} className="flex items-center justify-between p-2 bg-gray-700 rounded text-sm">
                        <span className={isEnabled ? 'text-green-400' : 'text-gray-500'}>
                          {service.display_name}
                        </span>
                        <div className="flex gap-1">
                          <button
                            onClick={() => toggleServiceOverride(service.name, 'add')}
                            className={`p-1 rounded ${inAdd ? 'bg-green-600' : 'bg-gray-600 hover:bg-gray-500'}`}
                            title=""
                          >
                            <Plus className="w-3 h-3" />
                          </button>
                          <button
                            onClick={() => toggleServiceOverride(service.name, 'remove')}
                            className={`p-1 rounded ${inRemove ? 'bg-red-600' : 'bg-gray-600 hover:bg-gray-500'}`}
                            title=""
                          >
                            <Minus className="w-3 h-3" />
                          </button>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>

              {/* Caps Override */}
              <div className="mb-4">
                <label className="block text-sm text-gray-400 mb-2">
                   
                </label>
                <div className="space-y-2">
                  {services.filter(s => s.caps.length > 0 && userEntitlements.enabled_services.includes(s.name)).map(service => (
                    <div key={service.name} className="bg-gray-700 rounded p-2">
                      <div className="text-xs text-gray-400 mb-1">{service.display_name}</div>
                      {service.caps.map(cap => (
                        <div key={cap} className="flex items-center gap-2">
                          <span className="text-xs text-gray-500 flex-1 truncate">{cap}:</span>
                          <input
                            type="number"
                            value={overrideCaps[service.name]?.[cap] ?? ''}
                            onChange={e => updateCapOverride(
                              service.name,
                              cap,
                              e.target.value ? parseInt(e.target.value) : null
                            )}
                            placeholder=""
                            className="w-20 bg-gray-600 border border-gray-500 rounded px-2 py-1 text-xs text-white"
                          />
                        </div>
                      ))}
                    </div>
                  ))}
                </div>
              </div>

              {/* Save Button */}
              <button
                onClick={saveOverrides}
                disabled={loading}
                className="w-full flex items-center justify-center gap-2 px-4 py-3 bg-yellow-600 hover:bg-yellow-500 disabled:bg-gray-600 rounded-lg font-medium"
              >
                <Save className="w-5 h-5" />
                 
              </button>
            </div>

            {/* Service Caps Info */}
            <div className="bg-gray-800 rounded-xl p-6">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <div className="space-y-2 text-sm">
                {Object.entries(userEntitlements.service_caps).map(([service, caps]) => (
                  <div key={service} className="bg-gray-700 rounded p-3">
                    <div className="font-medium text-gray-300">{service}</div>
                    {Object.entries(caps).map(([key, value]) => (
                      <div key={key} className="text-xs text-gray-500">
                        {key}: {formatNumber(value as number)}
                      </div>
                    ))}
                  </div>
                ))}
                {Object.keys(userEntitlements.service_caps).length === 0 && (
                  <p className="text-gray-500 text-center py-4">  </p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Empty State */}
      {!userEntitlements && !loading && (
        <div className="text-center py-20">
          <User className="w-16 h-16 text-gray-600 mx-auto mb-4" />
          <h3 className="text-xl font-semibold text-gray-400"> </h3>
          <p className="text-gray-500 mt-2">        </p>
        </div>
      )}
    </div>
  );
};

export default UserEntitlementsDashboard;
