// BanUserModal.tsx - Modal for banning users
import React, { useState } from 'react';
import { Ban, X, AlertTriangle } from 'lucide-react';
import ModalBase from '../ModalBase';
import { toast } from 'sonner';
import { apiPost } from '../../utils/api';
import { logger } from '../../utils/logger';

interface BanUserModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
  userId: string;
  userEmail: string;
}

const BanUserModal: React.FC<BanUserModalProps> = ({
  isOpen,
  onClose,
  onSuccess,
  userId,
  userEmail
}) => {
  const [reason, setReason] = useState('');
  const [saving, setSaving] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!reason.trim()) {
      toast.error('   ');
      return;
    }

    setSaving(true);
    try {
      await apiPost(`/api/admin/users/${userId}/ban`, { reason: reason.trim() }, { showErrorToast: false });
      toast.success(` ${userEmail}  `);
      onSuccess();
      onClose();
      setReason(''); // Reset
    } catch (error) {
      logger.error('Failed to ban user:', error);
      toast.error('  ');
    } finally {
      setSaving(false);
    }
  };

  const handleClose = () => {
    setReason(''); // Reset on close
    onClose();
  };

  return (
    <ModalBase isOpen={isOpen} onClose={handleClose} title=" " size="md">
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Warning Banner */}
        <div className="bg-red-500/10 border border-red-500/30 rounded-lg p-4">
          <div className="flex items-start gap-3">
            <AlertTriangle className="w-6 h-6 text-red-400 flex-shrink-0 mt-0.5" />
            <div>
              <h3 className="text-red-400 font-bold"></h3>
              <p className="text-red-200 text-sm mt-1">
                       .
                      .
              </p>
            </div>
          </div>
        </div>

        {/* User Info */}
        <div className="bg-white/5 rounded-lg p-4">
          <div className="text-sm text-slate-400"> </div>
          <div className="text-white font-medium text-lg">{userEmail}</div>
          <div className="text-xs text-slate-500 mt-1 font-mono">{userId}</div>
        </div>

        {/* Reason Input */}
        <div>
          <label className="block text-white font-medium mb-2">
              <span className="text-red-400">*</span>
          </label>
          <textarea
            value={reason}
            onChange={(e) => setReason(e.target.value)}
            placeholder=":   ,   ..."
            rows={4}
            required
            className="w-full px-4 py-3 bg-white/5 border border-white/20 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-red-500 resize-none"
          />
          <div className="text-sm text-slate-400 mt-1">
            {reason.length}/500 
          </div>
        </div>

        {/* Actions */}
        <div className="flex gap-3 justify-end pt-4 border-t border-white/10">
          <button
            type="button"
            onClick={handleClose}
            className="px-6 py-2 bg-slate-700 hover:bg-slate-600 text-white rounded-lg transition-colors flex items-center gap-2"
          >
            <X className="w-4 h-4" />
            
          </button>
          <button
            type="submit"
            disabled={saving || !reason.trim()}
            className="px-6 py-2 bg-red-600 hover:bg-red-700 disabled:bg-slate-700 disabled:cursor-not-allowed text-white rounded-lg transition-colors flex items-center gap-2"
          >
            <Ban className="w-4 h-4" />
            {saving ? '...' : ' '}
          </button>
        </div>
      </form>
    </ModalBase>
  );
};

export default BanUserModal;
