/**
 * Error Logs Dashboard - Frontend Error Viewer
 *  P1 FIX: UI for viewing frontend errors
 */

import React, { useState, useEffect } from 'react';
import {
  AlertTriangle, RefreshCw, Loader2, Search,
  Calendar, Globe, X
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet } from '../../utils/api';
import { logger } from '../../utils/logger';
import { formatDateTime } from '../../utils/formatters';

interface ErrorLog {
  id: string;
  message: string;
  stack?: string;
  componentStack?: string;
  timestamp: string;
  userAgent?: string;
  url?: string;
  user_id?: string;
  logged_at: string;
}

interface ErrorStats {
  total_errors: number;
  errors_last_24h: number;
  errors_last_hour: number;
  max_stored: number;
}

const ErrorLogsDashboard = () => {
  const [errors, setErrors] = useState<ErrorLog[]>([]);
  const [stats, setStats] = useState<ErrorStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [selectedError, setSelectedError] = useState<ErrorLog | null>(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [limit, setLimit] = useState(100);

  useEffect(() => {
    loadData();
    // Auto-refresh every 30 seconds
    const interval = setInterval(loadData, 30000);
    return () => clearInterval(interval);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [limit]);

  const loadData = async () => {
    try {
      const [errorsData, statsData] = await Promise.all([
        apiGet(`/api/errors/list?limit=${limit}`, { showErrorToast: false }),
        apiGet('/api/errors/stats', { showErrorToast: false })
      ]);

      setErrors(Array.isArray(errorsData) ? errorsData : []);
      setStats(statsData);
    } catch (error) {
      logger.error('Failed to load errors:', error);
      toast.error('  ');
    } finally {
      setLoading(false);
    }
  };

  const filteredErrors = errors.filter(error => {
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      return (
        error.message.toLowerCase().includes(query) ||
        (error.url && error.url.toLowerCase().includes(query)) ||
        (error.userAgent && error.userAgent.toLowerCase().includes(query))
      );
    }
    return true;
  });

  //  Using shared formatDateTime from utils/formatters

  if (loading && !stats) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="animate-spin text-blue-400" size={32} />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-br from-slate-800 to-slate-900 rounded-2xl p-6 border border-white/10">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-3">
            <AlertTriangle className="text-red-400" size={32} />
            <div>
              <h2 className="text-2xl font-bold text-white"> </h2>
              <p className="text-red-300 text-sm">  </p>
            </div>
          </div>
          <button
            onClick={loadData}
            disabled={loading}
            className="px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-600 text-white rounded-lg flex items-center gap-2"
          >
            <RefreshCw className={loading ? 'animate-spin' : ''} size={16} />
            
          </button>
        </div>

        {/* Stats */}
        {stats && (
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mt-4">
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="text-slate-400 text-sm mb-1">" </div>
              <div className="text-2xl font-bold text-white">{stats.total_errors}</div>
            </div>
            <div className="bg-red-500/20 rounded-lg p-4 border border-red-500/30">
              <div className="text-red-400 text-sm mb-1"> 24 </div>
              <div className="text-2xl font-bold text-red-400">{stats.errors_last_24h}</div>
            </div>
            <div className="bg-orange-500/20 rounded-lg p-4 border border-orange-500/30">
              <div className="text-orange-400 text-sm mb-1"> </div>
              <div className="text-2xl font-bold text-orange-400">{stats.errors_last_hour}</div>
            </div>
            <div className="bg-slate-700/50 rounded-lg p-4 border border-slate-600">
              <div className="text-slate-400 text-sm mb-1"> </div>
              <div className="text-2xl font-bold text-white">{stats.max_stored}</div>
            </div>
          </div>
        )}
      </div>

      {/* Search */}
      <div className="bg-slate-800 rounded-xl p-4 border border-white/10">
        <div className="flex gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-slate-400" size={18} />
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder=" ..."
              className="w-full pl-10 pr-4 py-2 bg-slate-700 border border-slate-600 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
          <select
            value={limit}
            onChange={(e) => setLimit(Number(e.target.value))}
            className="px-4 py-2 bg-slate-700 border border-slate-600 rounded-lg text-white"
          >
            <option value={50}>50</option>
            <option value={100}>100</option>
            <option value={200}>200</option>
            <option value={500}>500</option>
          </select>
        </div>
      </div>

      {/* Errors List */}
      <div className="bg-slate-800 rounded-xl border border-white/10 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-slate-700/50">
              <tr>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300">URL</th>
                <th className="px-4 py-3 text-right text-sm font-semibold text-slate-300"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-700">
              {filteredErrors.length === 0 ? (
                <tr>
                  <td colSpan={4} className="px-4 py-8 text-center text-slate-400">
                      
                  </td>
                </tr>
              ) : (
                filteredErrors.map((error) => (
                  <tr key={error.id} className="hover:bg-slate-700/50">
                    <td className="px-4 py-3 text-slate-400 text-sm">{formatDateTime(error.logged_at || error.timestamp)}</td>
                    <td className="px-4 py-3">
                      <div className="text-slate-200 text-sm max-w-md truncate" title={error.message}>
                        {error.message}
                      </div>
                    </td>
                    <td className="px-4 py-3 text-slate-400 text-sm max-w-xs truncate" title={error.url}>
                      {error.url || 'N/A'}
                    </td>
                    <td className="px-4 py-3">
                      <button
                        onClick={() => setSelectedError(error)}
                        className="px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-sm"
                      >
                        
                      </button>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Error Details Modal */}
      {selectedError && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-slate-800 rounded-2xl p-6 max-w-4xl w-full max-h-[90vh] overflow-y-auto border border-white/10">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-xl font-bold text-white"> </h3>
              <button
                onClick={() => setSelectedError(null)}
                className="text-slate-400 hover:text-white"
              >
                <X size={24} />
              </button>
            </div>

            <div className="space-y-4">
              <div>
                <div className="text-slate-400 text-sm mb-1"></div>
                <div className="text-white bg-slate-900 rounded-lg p-3">{selectedError.message}</div>
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <div className="text-slate-400 text-sm mb-1 flex items-center gap-2">
                    <Calendar size={16} />
                    
                  </div>
                  <div className="text-white">{formatDateTime(selectedError.logged_at || selectedError.timestamp)}</div>
                </div>
                {selectedError.url && (
                  <div>
                    <div className="text-slate-400 text-sm mb-1 flex items-center gap-2">
                      <Globe size={16} />
                      URL
                    </div>
                    <div className="text-white text-sm break-all">{selectedError.url}</div>
                  </div>
                )}
              </div>
              {selectedError.userAgent && (
                <div>
                  <div className="text-slate-400 text-sm mb-1">User Agent</div>
                  <div className="text-white text-sm bg-slate-900 rounded-lg p-3 break-all">{selectedError.userAgent}</div>
                </div>
              )}
              {selectedError.stack && (
                <div>
                  <div className="text-slate-400 text-sm mb-1">Stack Trace</div>
                  <pre className="bg-slate-900 border border-slate-700 rounded-lg p-3 text-xs text-slate-300 overflow-x-auto">
                    {selectedError.stack}
                  </pre>
                </div>
              )}
              {selectedError.componentStack && (
                <div>
                  <div className="text-slate-400 text-sm mb-1">Component Stack</div>
                  <pre className="bg-slate-900 border border-slate-700 rounded-lg p-3 text-xs text-slate-300 overflow-x-auto">
                    {selectedError.componentStack}
                  </pre>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ErrorLogsDashboard;

